<template>
  <div class="bookmark container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <div class="k_ti">
            本棚
            <img class="cursor tips" v-on:click="this.$root.pushTips('BOOKMARK')" :src="this.$root.IMG_PATH + 'btn_hint' + this.$root.theme_img">
          </div>

          <div class="grayBar" />

          <select class="form-select" :class="this.$root.theme_bc_2" name="series" v-on:change="changeSeries" v-model="selectSeries">
            <option v-for="(seriesName, index) in seriesList" :key="index" v-bind:value="seriesName">{{seriesName}}</option>
          </select>
          <div class="grayBar" />
          <post-list-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></post-list-row>
          <subPagenation
            :showPages="showPages"
            :currentPage="currentPage"
            :totalCount="totalCount"
            :perPage="perPage"
            :totalPages="totalPages"
            @currentPage="getCurrentPage"
          ></subPagenation>
        </div>
        <br>
      </div>
    </div>
  </div>
</template>

<script>
import SubPagenation from './SubPagenation.vue';
import axios from 'axios'

export default {
  name: 'Bookmark',
  components: {
    SubPagenation
  },
  data() {
    return {
      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 10, //1ページの表示件数
      totalCount: 0, //取得したアイテムの総件数
      totalPages: 0, //総ページ数

      selectSeries : "",
      allPostDataList: [],
      filterDataList : [],
      dispDataList: [],
      seriesList: ["すべて"]
    };
  },
  computed : {
  },
  methods : {
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    changeSeries : function(e) {
      let query = Object.assign({}, this.$route.query);
      query["series"] = e.target.value;
      query["page"] = 1;
      this.$router.push({query});
    },
    getBookmarkList : function() {
      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + 'getBookmarkList', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        let resData = response.data;
        let resDataList = resData["bookmarkList"];

        this.allPostDataList = [];
        for (let i = 0; i < resDataList.length; i++) {
          let resData = resDataList[i];

          var tagStr = resData["tags"];
          var tagArray = {};
          if (tagStr != "") {
            tagArray = tagStr.split(',');
          }

          var data = {
            postId : resData["post_id"],
            userId : resData["user_id"],
            listType : "listTypeA",
            author : resData["user_name"],
            series : resData["series"],
            title : resData["title"],
            outline : resData["outline"],
            rating : resData["rating"],
            chara : resData["chara"],
            creation : resData["creation"],
            tags : tagArray,
            length : resData["length"],
            publishing : resData["publishing"],
            postDate : new Date(resData["created_at"])
          };
          this.allPostDataList.push(data);
        }
        
        //シリーズ一覧取得
        this.seriesList = this.$root.retComSeriesList(this.allPostDataList);

        //初期処理
        this.initAction();
      }).catch((error) => {          
        this.$root.comNetworkError(error);
      });
    },
    initAction : function() {
      //パラメータチェック
      let query = Object.assign({}, this.$route.query);
      let page = query["page"];
      let series = query["series"];

      //ページが存在しないまたはマイナス
      if (page == null || page < 0) {
        query["page"] = 1;
        this.$router.replace({query});
        return;
      }
      //シリーズが存在しない場合
      if (series == null) {
        query["series"] = "すべて";
        this.$router.replace({query});
        return;
      }

      //シリーズチェック
      let seriesFlg = false;
      for (let i = 0; i < this.seriesList.length; i++) {
        if (this.seriesList[i] == series) {
          seriesFlg = true;
        }
      }
      //シリーズが存在しない場合
      if (seriesFlg == false) {
        query["series"] = "すべて";
        this.$router.replace({query});
        return;
      }

      this.currentPage = page;
      this.selectSeries = series;

      //シリーズから絞り込み
      this.filterDataList = this.$root.retComFilterList(this.allPostDataList, series);
      this.$root.log(this.filterDataList);

      //ページ数
      this.totalCount = this.filterDataList.length;
      var tPage = Math.floor(this.totalCount / 10);
      if (this.totalCount % 10 != 0) {
        tPage = tPage + 1;
      }
      this.totalPages = tPage;

      //ページ数が最大を超えている場合
      if (this.totalPages < this.currentPage) {
        query["page"] = this.totalPages;
        this.$router.replace({query});
      }

      //ページを表示
      this.dispDataList = this.$root.retComPagingList(this.currentPage, this.filterDataList);

      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getBookmarkList();
      vm.$root.setCurrentPage("BOOKMARK");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getBookmarkList();
      vm.$root.setCurrentPage("BOOKMARK");
      this.$root.closePopup();
    });
  },
  watch : {
    '$route'() {
      if (this.$route.name == "Bookmark") {
        this.$root.showLoad();
        this.initAction();
      }
    }
  }
}
</script>

