<template>
  <div class="follow container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <div class="k_ti">
            推し作家
          </div>

          <div class="grayBar" />

          <user-list-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></user-list-row>
          <subPagenation
            :showPages="showPages"
            :currentPage="currentPage"
            :totalCount="totalCount"
            :perPage="perPage"
            :totalPages="totalPages"
            @currentPage="getCurrentPage"
          ></subPagenation>
        </div>
        <br>
      </div>
      <ad-area></ad-area>
    </div>
  </div>
</template>

<script>
import SubPagenation from './SubPagenation.vue';
import axios from 'axios'

export default {
  name: 'Follow',
  components: {
    SubPagenation
  },
  data() {
    return {
      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 10, //1ページの表示件数
      totalCount: 15, //取得したアイテムの総件数
      totalPages: 3, //総ページ数

      filterDataList : [],
      dispDataList: []
    };
  },
  computed : {
  },
  methods : {
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    getFollowList : function() {
      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + 'getFollowList', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        let resData = response.data;
        let resDataList = resData["followList"];
        this.filterDataList = [];
        for (let i = 0; i < resDataList.length; i++) {
          let resData = resDataList[i];

          var data = {
            userId : resData["follow_id"],
            userName : resData["user_name"],
            profile : resData["profile"],
          };

          this.filterDataList.push(data);
        }

        //初期処理
        this.initAction();

      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    initAction : function() {
      //パラメータチェック
      let query = Object.assign({}, this.$route.query);
      let page = query["page"];
      let series = query["series"];

      //ページが存在しないまたはマイナス
      if (page == null || page < 0) {
        query["page"] = 1;
        this.$router.replace({query});
        return;
      }

      this.currentPage = page;

      //ページ数
      this.totalCount = this.filterDataList.length;
      var tPage = Math.floor(this.totalCount / 10);
      if (this.totalCount % 10 != 0) {
        tPage = tPage + 1;
      }
      this.totalPages = tPage;

      //ページ数が最大を超えている場合
      if (this.totalPages < this.currentPage) {
        query["page"] = this.totalPages;
        this.$router.replace({query});
      }

      //ページを表示
      this.dispDataList = this.$root.retComPagingList(this.currentPage, this.filterDataList);

      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getFollowList();
      vm.$root.setCurrentPage("FOLLOW");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getFollowList();
      vm.$root.setCurrentPage("FOLLOW");
    });
  },
  watch : {
    '$route'() {
      if (this.$route.name == "Follow") {
        this.$root.showLoad();
        this.initAction();
      }
    }
  }
}
</script>

