  methods : {
    push : function() {

      let params = new FormData();
      params.append('iconFile', this.setIconFile);
      params.append('iconFileName', this.setIconFileName);

      let config = {
          headers: {'Content-Type': 'multipart/form-data'}
      };
      console.log(params);
      axios.post(this.$root.SERVER_PATH + 'test', params, config)
      .then((response) => {
        console.log(response);
      });
    },
    onFileChange(e) {

      const files = e.target.files || e.dataTransfer.files;
      if (files.length == 0) {
        return;
      }
      let size = files[0].size;
      let type = files[0].type;

      this.$root.log(size + " " + type);

      // this.createImage(files[0]);
      this.setIconFile = files[0];
      this.setIconFileName = files[0].name;

    },
    test : function () {
      let password = "111111@@@";

      let params = new URLSearchParams();

      params.append('password', password);
      axios.post(this.$root.SERVER_PATH + 'tete', params)
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
