<template>
  <div class="requestResetPassword" style="text-align:center; width:270px; margin:auto">
    <div v-show="errorMessageData.init != ''">
      <div style="height:36px" />
      <span class="p12">パスワードリセットエラー</span>
      <div style="height:20px" />
      <span class="p9">{{ errorMessageData.init }}</span>
      <div style="height:38px" />
      <span class="btn" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="this.$root.pushHome">トップへ</span>
      <br>
      <br>
      <br>
      <br>
    </div>
    <div v-show="errorMessageData.init == ''">
      <div v-show="isSend == false">
        <div style="height:36px" />
        <span class="p12">パスワード再発行</span>
        <div style="height:22px" />
        <span class="p9">新しいパスワードを入力し「再設定」ボタンを押してください</span>
        <div style="height:15px" />
        <p class="errMsg" v-if="errorMessageData.password1">▼ {{errorMessageData.password1}}</p>
        <input type="password" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" style="width:100%" v-model="newPassword1" maxlength="32" autocomplete="new-password" :placeholder="this.$root.PLACEHOLDER_PASSWORD">
        <div style="height:10px" />
        <p class="errMsg" v-if="errorMessageData.password2">▼ {{errorMessageData.password2}}</p>
        <input type="password" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" style="width:100%" v-model="newPassword2" maxlength="32" autocomplete="new-password" :placeholder="this.$root.PLACEHOLDER_PASSWORD">
        <div style="height:12px" />
        <p class="errMsg" v-if="errorMessageData.reset">▼ {{errorMessageData.reset}}</p>
        <span class="btn" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="pushReset">再設定</span>
        <br>
        <br>
        <br>
        <br>
      </div>
      <div v-show="isSend == true">
        <div style="height:36px" />
        <span class="p12">パスワード設定完了</span>
        <div style="height:22px" />
        <span class="p9">パスワードの再設定が完了しました</span>
        <div style="height:25px" />
        <span class="btn btn-icon btn-icon-key" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="this.$root.pushHome">トップへ</span>
        <br>
        <br>
        <br>
        <br>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'RequestResetPassword',
  components: {
  },
  data() {
    return {
      isSend : false,

      newPassword1 : "",
      newPassword2 : "",

      errorMessageData : {
        init : "",
        password1 : "",
        password2 : "",
        reset : "",
      }
    };
  },
  computed : {
  },
  methods : {
    pushReset : function () {
      this.$root.showLoad();

      this.errorMessageData.password1 = "";
      this.errorMessageData.password2 = "";
      this.errorMessageData.reset = "";

      let tempId = this.$route.params.tempId;
      
      //異常値チェック
      if (tempId == null || tempId.length != 20) {
        //エラー
        this.errorMessageData.init = "このURLは無効なパラメータが含まれているか、有効期限が切れています";
        this.$root.closePopup();
        return;
      }

      //トリム
      this.newPassword1 = this.newPassword1.trim();
      this.newPassword2 = this.newPassword2.trim();

      //入力チェック
      if (this.newPassword1.length < 8) {
        this.errorMessageData.password1 = "新しいパスワードの文字数は8文字以上入力してください";
      } else if (this.newPassword1.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.password1 = "新しいパスワードの文字数は32文字以下で入力してください";
      } else if (!this.$root.checkPassword(this.newPassword1)) {
        this.errorMessageData.password1 = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.newPassword2.length < 8) {
        this.errorMessageData.password2 = "新しいパスワード（確認）の文字数は8文字以上入力してください";
      } else if (this.newPassword2.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.password2 = "新しいパスワード（確認）の文字数は32文字以下で入力してください";
      } else if (!this.$root.checkPassword(this.newPassword2)) {
        this.errorMessageData.password2 = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.newPassword1 != this.newPassword2) {
        this.errorMessageData.password2 = "新しいパスワード（確認）が正しくありません";
      }

      if (this.errorMessageData.password1 != "" || this.errorMessageData.password2 != "") {
        this.$root.closePopup();
        return;
      }
      
      //仮登録処理
      let params = new URLSearchParams();
      params.append('tempId', tempId);
      params.append('newPassword', this.newPassword1);

      axios.post(this.$root.SERVER_PATH + 'resetPassword', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_NODATA || resData["status"] == this.$root.API_FAILED_PARAM) {
          //パラメータ異常、データなし
          this.$router.push({path : "/error", query : {errorCode : resData["status"], message : resData["errMsg"]}});
        }

        if (!this.$root.checkServerResponse(response, false)) {
          return;
        }

        this.isSend = true;
        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    pushLogin : function () {
      this.$router.push('/login');
    },
    initAction : function () {
      this.errorMessageData.init = "";
      let tempId = this.$route.params.tempId;
      
      if (tempId == null || tempId.length != 20) {
        this.errorMessageData.init = "このURLは無効なパラメータが含まれているか、有効期限が切れています";
        this.$root.closePopup();
        return;
      }

      let params = new URLSearchParams();
      params.append('tempId', tempId);

      axios.post(this.$root.SERVER_PATH + 'resetPasswordInit', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_PARAM) {
          //該当データなし
          this.errorMessageData.init = "このURLは無効なパラメータが含まれているか、有効期限が切れています";
          this.$root.closePopup();
          return;
        }

        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          if (resData["errMsg"] == "TEMP") {
            //TEMPテーブルになし
            this.errorMessageData.init = "このURLは無効なパラメータが含まれているか、有効期限が切れています";
            this.$root.closePopup();
            return;
          } else {
            //USERテーブルになし
            this.errorMessageData.init = "該当のアカウントが見つかりません";
            this.$root.closePopup();
            return;
          }
        }

        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.initAction();
    });
  }
}
</script>

