    created() {
      console.log("getAds");
      //テーマ取得
      let themeData = localStorage.getItem("theme");
      if (themeData == null) {
        this.themeIndex = 0;
        localStorage.setItem("theme", "0");
      } else {
        let theme = parseInt(themeData, 10);
        if (theme != 0 && theme != 1 && theme != 2 && theme != 3) {
          theme = 0;
          localStorage.setItem("theme", "0");
        }
        this.themeIndex = theme;
      }

      //自社広告用
      axios.get(this.$root.SERVER_PATH + 'getAds')
      .then((response) => {
        console.log(response.data);
        this.adInfoArray = response.data["ads"];

        if (this.adInfoArray.length != 0) {
          console.log("広告スタート");
          //広告スタート
          const adFunc = function () {
            this.adIndex = this.adIndex + 1;
            if (this.adIndex == this.adInfoArray.length) {
              this.adIndex = 0;
            }
          }.bind(this);

          setInterval(adFunc, 10000);
        }
      });
