        } else if (index == "EDIT_PUBLISHING") { this.showPopup("TIPS", "▼公開方法", "以下のいずれかの公開方法を必ず選択してください。\n\n◆全体公開\nyumedropに登録しているすべての読者に公開されます。\n全体公開していても「検索一覧に載せる」の項目で「ＯＦＦ」にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆Twitter相互フォロワー限定\nyumedropに登録している読者のうち、Twitterアカウントの相互フォロワーにのみ公開されます。\nTwitter相互フォロワー限定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆Twitterフォロワー限定\nyumedropに登録している読者のうち、Twitterアカウントのフォロワーにのみ公開されます。\nTwitterフォロワー限定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆Twitterリスト限定\nyumedropに登録している読者のうち、Twitterアカウントでリストに登録したユーザーにのみ公開されます。\nTwitterリスト限定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆パスワード\n投稿作品を読むためのパスワードのヒント、パスワードを設定できます。\n半角全角問わず、ヒントは50文字まで、パスワードは20文字まで入力できます。\nパスワード付きの設定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆非公開\nyumedropに登録しているすべての読者が読めなくなります。\n非公開の設定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。");
        } else if (index == "EDIT_SEARCHABLE") { this.showPopup("TIPS", "▼検索一覧に載せる", "検索一覧に載せない場合、新着一覧にも表示されません。\nまた、全体公開以外は、自動的に新着一覧、検索一覧に掲載されなくなります。");
        } else if (index == "SETTING_ICON") { this.showPopup("TIPS", "▼プロフィール画像", "自分のプロフィール画像を設定できます。\n\n注意）\n自分自身が財産権（著作権、肖像権など）を有していない画像データの取り扱いは、該当の利用規約やガイドライン、指示に従ってアイコンとして使用してください。アニメやコミックス、ゲームなどのスクリーンショットなどの取り扱いにも注意してください。");
        } else if (index == "SETTING_NAME") { this.showPopup("TIPS", "▼ユーザー名", "自分のユーザー名を入力してください。\n本サービス利用者に公開されるため、個人情報の取り扱いには十分留意してください。");
        } else if (index == "SETTING_PROFILE") { this.showPopup("TIPS", "▼自己紹介", "自己紹介を入力してください。\n本サービス利用者に公開されるため、個人情報の取り扱いには十分留意してください。");
        } else if (index == "SETTING_LINK") { this.showPopup("TIPS", "▼リンク", "外部サイトのURLが入力できます。\n\n注意）\n違法サイト、連鎖販売サイト、ポイントサイト、その他の不適切なサイトへのリンクは入力しないでください。");
        } else if (index == "SETTING_SHARE") { this.showPopup("TIPS", "▼マイページのURL", "自分の「ユーザー名」「自己紹介」「投稿作品一覧」が掲載されたページのURLです。");
        } else if (index == "SETTING_FAVORITE") { this.showPopup("TIPS", "▼お気に入りタグ", "お気に入りのタグを8個まで登録できます。\nお気に入りのタグは検索ページに一覧表示され、該当のジャンルタグのついた夢小説を探しやすくなります。");
        } else if (index == "SETTING_MUTE_TAG") { this.showPopup("TIPS", "▼ミュートタグ", "ミュートタグを8個まで登録できます。\n自分の苦手なジャンル、作品傾向を避けることができます。");
        } else if (index == "SETTING_MUTE_USER") { this.showPopup("TIPS", "▼ユーザーミュート", "自分がミュートしているユーザーの一覧を表示します。\nミュートを解除するには、一覧に表示されている名前からユーザーのマイページに移動し、「ミュートを解除」ボタンを押してください。");
        } else if (index == "SETTING_DISP") { this.showPopup("TIPS", "▼閲覧制限", "レーティングによる表示制限です。\nＯＮにすることで該当の作品が表示されます。");
        } else if (index == "SETTING_TWITTER") { this.showPopup("TIPS", "▼Twitterアカウント", "本サービスに連携しているTwitterアカウントを表示しています。\n\n◆認証解除\n「認証解除」ボタンを押すと、本サービスに連携しているTwitterアカウントの認証が解除されます。\nメールアカウントの項目を設定していない場合、「認証解除」ボタンを押すことができません。\n\n◆新規認証／再認証\n新たにTwitterアカウントを連携する場合、再びTwitterアカウントを連携する場合は、「認証する」ボタンを押してください。");
        } else if (index == "SETTING_MAIL") { this.showPopup("TIPS", "▼メールアカウント", "本サービスに登録しているメールアドレスを表示しています。\nTwitterアカウントの不具合、凍結、削除などが起こった場合、本サービスにメールアカウントからもログインできます。\n本サービスに連携したメールアドレスはプロフィールに表示されません。\n\n◆未認証\n本サービスにメールアドレスが設定されていません。\n\n◆認証解除\n「認証解除」ボタンを押すと、本サービスに連携しているメールアドレスの認証が解除されます。\n\n◆新規認証／再認証\n新たにメールアドレスを連携する場合、再びメールアドレスを連携する場合は、正しく「メールアドレス」と「パスワード（8文字以上）」を入力し、「認証する」ボタンを押してください。\nその後、本サービスからメールアカウントの登録確認メールを送信します。受信したメールに記載されているURLより、手続きを完了してください。\n\n注意）\n本サービスから登録確認メールが届かない場合、正しくメールアドレスが入力されていないか、迷惑メールに振り分けられている可能性があります。\n\nまた、yumedropからのメールが受信できるよう、ドメイン指定受信で「yumedrop.com」を許可してください。");
        } else if (index == "SETTING_INFO") { this.showPopup("TIPS", "▼お知らせ", "本サービスに関する情報をメールでお知らせします。\n設定を「ＯＦＦ」にしても、重要なお知らせはメールいたします。");
        } else if (index == "SETTING_WITHDRAWL") { this.showPopup("TIPS", "▼退会", "すべてのデータが削除されます。\n一度削除されたデータは、お問い合わせいただいても、復元することができません。");
        } else if (index == "SETTING_DISP_TWITTER") { this.showPopup("TIPS", "▼プロフィールにTwitterアカウントを表示する", "本サービスに連携したTwitterアカウントをプロフィールに表示します。\n表示しない場合は「ＯＦＦ」にしてください。");
        } else if (index == "SETTING_RESET_PASSWORD") { this.showPopup("TIPS", "▼パスワード変更", "パスワードを変更します。\n現在のパスワード、新しいパスワード、新しいパスワード（確認）を入力し、「変更する」ボタンを押してください。");
        } else if (index == "POSTLIST") { this.showPopup("TIPS", "▼投稿一覧", "投稿した作品一覧を表示します。\n\n◆アイコン\n下記のアイコンとともに表示されている数字は投稿した作家のみ閲覧可能です。\n\nハートアイコン：応援スタンプが押された回数\nしおりアイコン：本棚に追加された回数\n目のアイコン　：作品の閲覧数\n\n◆シリーズ選択\n作家が設定したシリーズ名ごとに作品を表示します。\n\n◆作品を編集\n投稿した作品を編集することができます。\nこれまでの応援スタンプが押された回数、本棚に追加された回数、作品の閲覧数はリセットされません。\nなお、投稿日のタイムスタンプは変更できません。");
        } else if (index == "BOOKMARK") { this.showPopup("TIPS", "▼本棚", "本棚に追加した作品一覧を表示します。\n本棚から削除したい場合は、作品閲覧ページの下部から「本棚から返却」を選択してください。");
        } else if (index == "EDIT") { this.showPopup("TIPS", "▼作品を投稿", "◆タイトル\n作品のタイトルを50文字以内で入力してください。\n\n◆説明\n作品についての説明を200文字以内で入力してください。\n\n◆本文\n小説本文を入力してください。\n\n名前変換のテクニック）\n読者に「完全一致」で名前を変換してもらいたい場合、本文に入力する名前は「#夢野#」「#アメ#」など、変換したい文字を「#（半角ハッシュ）」で閉じて入力してください。\nなお、本文中に登場する文字と被らない文字を変換させる場合は「夢野」「アメ」のように「#（半角ハッシュ）」を省略して入力することが可能です。\n\nその他、絵文字や記号を変換させることも可能ですが、対応外の絵文字や記号もあります。何卒ご了承ください。");
        } else if (index == "READING_CONVERSION") { this.showPopup("TIPS", "▼変換項目", "特定のワード（名前、地名、アイテム名など……）を変換して作品を読むことができます。\n作家からの問いかけに対し、変換する文字を入力してください。\n変換できる文字数は10文字までです。");
        } else if (index == "READING_STAMP") { this.showPopup("TIPS", "▼応援スタンプ", "作家に応援の気持ちを込めてスタンプを送ることができます。\n１作品に押せるスタンプは10個までです。");
        }

      },
      chNull : function (val) {
        if (val == null) {
          return "";
        } else {
          return val;
        }
      },
      retComSeriesList : function(dataList) {
        let seriesList = [];
        seriesList.push("すべて");

        for (let i = 0; i < dataList.length; i++) {
          var series = dataList[i].series;
          var flg = true;
          for (let j = 0; j < seriesList.length; j++) {
            if (seriesList[j] == series) {
              flg = false;
            }
          }
          if (flg) {
            seriesList.push(series);
          }
        }
        return seriesList;
      },
      retComFilterList : function(dataList, series) {
        let filterDataList = [];
        for (let i = 0; i < dataList.length; i++) {
          var data = dataList[i];
          //シリーズの絞り込み
          if (series == "すべて" || data.series == series) {
            filterDataList.push(data);
          }
        }
        return filterDataList;
      },
      retComPagingList : function (page, dataList) {
        let retArray = [];
        if (dataList.length == 0) {
          return retArray;
        }

        let max = (page - 1) * 10 + 10;
        if (dataList.length < (page - 1) * 10 + 10) {
          max = dataList.length;
        }

        for (let i = (page - 1) * 10; i < max; i++) {
          retArray.push(dataList[i]);
        }
        return retArray;
      },
      checkAddress : function (mailAddress) {
        if (mailAddress.indexOf('@') == -1) {
          return 1;
        } else {
          let array = mailAddress.split('@');
          
          if (array.length != 2) {
            return 1;
          } else {
            let domain = array[1];

            if (domain.indexOf('.jp') == -1 && domain.indexOf('.com') == -1 && domain.indexOf('.net') == -1 && 
              domain.indexOf('.org') == -1 && domain.indexOf('.xyz') == -1) {
              return 2;
            }
          }
        }
        return 0;
      },
      checkPassword : function (password) {
        const regex = /^[a-zA-Z0-9!?&$%+-/#]+$/;
        return regex.test(password);
      },
      checkRegulation : function (str) {
        for (let i = 0; i < this.regulationArray.length; i++) {
          if (str.indexOf(this.regulationArray[i]) != -1) {
            return this.regulationArray[i];
          }
        }
        return "";
      }
    }
  }
</script>

