<template>
  <div class="editresult container-fluid">
    <p>{{errorCode}}</p>
    <br>
    <div v-if="errorMessage!=''">
      {{errorMessage}}
    </div>
    <router-link to="#" v-on:click.prevent.stop="pushBtn">メニューへ</router-link>
  </div>
</template>

<script>
export default {
  name: 'Error',
  components: {
  },
  data() {
    return {
      errorCode : "",
      errorMessage : "",
    };
  },
  computed : {
  },
  methods : {
    pushBtn : function(btnName) {
      if (btnName == "menu") {
        this.$router.push("/mainmenu");
      }
    },
    setInit : function() {
      let query = Object.assign({}, this.$route.query);

      let code = query["errorCode"];
      let message = query["message"];
      console.log(code + "   " + message);
      this.errorMessage = message;
      if (code == this.$root.API_FAILED_LOGIN) {
        this.errorCode = "セッションエラーです";
      } else if (code == this.$root.API_FAILED_PARAM) {
        this.errorCode = "不正なパラメータを検知しました";
      } else if (code == this.$root.API_FAILED_NODATA) {
        this.errorCode = "データが存在しません";
      } else if (code == this.$root.API_FAILED_DUPLICATE) {
        this.errorCode = "システムエラー：複数の値を検知";
      } else if (code == this.$root.API_FAILED_MISMATCH) {
        this.errorCode = "システムエラー：値の不整合";
      } else if (code == this.$root.API_FAILED_PRIVATE) {
        this.errorCode = "このデータは非公開です";
      } else if (code == this.$root.API_FAILED_FILE) {
        this.errorCode = "ファイルアップロードエラー";
      } else if (code == this.$root.API_FAILED_EXEPTION) {
        this.errorCode = "システムエラー：予期せぬ例外";
      }

      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.setInit();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.setInit();
    });
  }
}
</script>

