<template>
  <div class="registration" style="text-align:center; width:270px; margin:auto">
    <div v-if="isSend == false">
      <div class="sp_line_m" />

      <p class="p18">ID（メールアドレス）とPASSで新規登録</p>
      
      <div class="sp_line_m" />
      
      <div style="text-align:left">
        <span class="errMsg" v-if="errorMessageData.id">▼ {{errorMessageData.id}}<br></span>
        <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" style="width:100%" v-model="id" maxlength="200" autocomplete="off" :placeholder="this.$root.PLACEHOLDER_MAIL"><br>
        
        <div class="sp_line_m" />
        
        <span class="errMsg" v-if="errorMessageData.password">▼ {{errorMessageData.password}}<br></span>
        <input type="password" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" style="width:100%" v-model="password" maxlength="32" autocomplete="new-password" :placeholder="this.$root.PLACEHOLDER_PASSWORD"><br>
      </div>
      <div class="sp_line_m" />
      <div id="CHK" :class="this.$root.theme_chk_1" style="display:flex;">
        <label class="typeA-checkbox">
          <input type="checkbox"  v-model="isTerms" true-value="1" false-value="0">
          <div class="indicator"></div>
        </label>
        <span style="margin-top:5px"><span class="link" :class="this.$root.theme_fc_1" v-on:click="this.$router.push('/terms')">利用規約</span>に同意しますか？</span>
      </div>

      <div class="sp_line_m" />

      <span class="errMsg" v-if="errorMessageData.login">▼ {{errorMessageData.login}}<br></span>
      <span v-show="isTerms == 1" class="btn btn-icon" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="pushRegist">IDとPASSで新規登録</span>
      <span v-show="isTerms == 0" class="btn btn-gray btn-icon" style="width:100%;">IDとPASSで新規登録</span>
      
      <div class="sp_line_m" />
      
      <div class="p14">既に登録済みの方はこちら</div>
      
      <div class="sp_line_s" />
      
      <span class="btn btn-yellow" style="width:100%" v-on:click="pushLogin">ログイン</span>
      <br>
      <br>
      <br>
      <br>
    </div>
    <div v-if="isSend && !isAuth">
      <!-- <div> -->
      <p class="p18">仮登録完了</p>

      <div class="sp_line_m" />

      <p class="p9">アカウント登録の確認メールを送信しました。<br>受信したメールに記載されているURLより、<br>本登録を完了してください。</p>

      <div class="sp_line_s" />

      <p class="p9">{{ kariMail }}</p>
      <span class="btn" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="pushHome">Topに戻る</span>
      <br>
      <br>
      <br>
      <br>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Registration',
  components: {
  },
  data() {
    return {
      isTerms : 0,
      isSend : false,
      isAuth : false,
      url : "",

      id : "",
      password : "",

      tempId : "",
      numbers: "",

      errorMessageData : {
        id : "",
        password : "",
        login : ""
      },

      kariMail : "",
    };
  },
  computed : {
  },
  methods : {
    pushTerms : function () {
      if (this.isTerms == 0) {
        this.isTerms = 1;
      } else {
        this.isTerms = 0;
      }
    },
    pushRegist : function () {
      this.$root.showLoad();

      this.errorMessageData.id = "";
      this.errorMessageData.password = "";
      this.errorMessageData.login = "";

      //トリム
      this.id = this.id.trim();
      this.password = this.password.trim();
      
      //入力チェック
      if (this.id == "") {
        this.errorMessageData.id = "ID（メールアドレス）の文字数は1文字以上入力してください";
      } else if (this.id.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.id = "ID（メールアドレス）の文字数は200文字以下で入力してください";
      } else {
        let val = this.$root.checkAddress(this.id);
        if (val == 1) {
          this.errorMessageData.id = "ID（メールアドレス）に誤りがあります。もう一度入力してください";
        } else if (val == 2) {
          this.errorMessageData.id = "該当のメールアドレスのドメインでは登録できません。別のメールアドレスでご登録ください";
        }
      }
      
      if (this.password.length < 8) {
        this.errorMessageData.password = "パスワードの文字数は8文字以上入力してください";
      } else if (this.password.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.password = "パスワードの文字数は32文字以下で入力してください";
      } else if (!this.$root.checkPassword(this.password)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.errorMessageData.id != "" || this.errorMessageData.password != "") {
        this.$root.closePopup();
        return;
      }
      
      console.log("OK");
      //仮登録処理
      let params = new URLSearchParams();
      params.append('mailAddress', this.id);
      params.append('password', this.password);
      params.append('kbn', "NEW");
      axios.post(this.$root.SERVER_PATH + 'requestRegister', params)
      .then((response) => {
        this.$root.log(response.data);
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
          //仮登録エラー（本登録重複）
          this.errorMessageData.id = "このメールアドレスは既に使用されています";
          return;
        } else if (resData["status"] == this.$root.API_FAILED_MISMATCH) {
          //フォーマットエラー
          this.errorMessageData.id = "メールアドレスの形式が正しくありません";
          return;
        }

        if (!this.$root.checkServerResponse(response, false)) {
          return;
        }

        this.tempId = resData["temp_id"];
        this.$root.log(resData["numbers"]);
        this.kariMail = "https://iphone-taro.sakura.ne.jp/yumedrop/registrationConfirmation/" + resData["temp_id"];
        this.isSend = true;
        this.$root.closePopup();   
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    pushLogin : function () {
      this.$router.push("/login");
    },
    pushHome : function () {
      this.$router.push('/mainmenu');
    },
    initAction : function() {
      axios.get(this.$root.SERVER_PATH + 'isAuth')
      .then((response) => {
        this.$root.log(response.data);

        if (!this.$root.checkServerResponse(response, false)) {
          return;
        }

        if (this.$root.isLogin) {
          //ログイン済みの場合はメインメニューへ
          this.$router.push("/mainmenu");
        } else {
          this.$root.closePopup();
        }
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.$root.setCurrentPage("");
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.$root.setCurrentPage("");
      vm.initAction();
    });
  }
}
</script>

