<script>

export default {
  name: 'MainMenu',
  props: {
  },
  components: {
  },
  data() {
    return {

    };
  },
  computed : {
  },
  methods : {
    pushBtn : function (name) {
      if (name == "search") {
        this.$router.push("/search");
      } else if (name == "edit") {
        this.$router.push("/edit/new");
      } else if (name == "postlist") {
        this.$router.push({ path:"/postlist", query : {series : "すべて", page : 1}});
      } else if (name == "bookmark") {
        this.$router.push({ path:"/bookmark", query : {series : "すべて", page : 1}});
      } else if (name == "follow") {
        this.$router.push({ path:"/follow", query : {page : 1}});
      } else if (name == "setting") {
        this.$router.push("/setting");
      } else if (name == "error") {
        this.$router.push({path : "/error", query : {errorCode : "99", message : "1"}});
      }
    },
    changeTheme : function(index) {
      this.$root.themeIndex = index;
      localStorage.setItem("theme", String(index));
    },
    initAction : function() {
      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.$root.getBaseInfo();
      vm.$root.setCurrentPage("MAINMENU");
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.$root.getBaseInfo();
      vm.$root.setCurrentPage("MAINMENU");
      vm.initAction();
    });
  }
}
</script>

