  methods : {
    initAction : function() {
      console.log(localStorage.getItem("login"));
      if (localStorage.getItem("login") != "") {
        //ログイン処理
        console.log("ログイン開始");
        localStorage.setItem("login", "");
        const provider = new TwitterAuthProvider()
        const auth = getAuth();
        setPersistence(auth, browserLocalPersistence).then(() => {
          signInWithRedirect(auth, provider);
        });
      } else {
        getRedirectResult(getAuth()).then((result) => {
          //連携成功時
          console.log(result);
          let params = new URLSearchParams();
          params.append('twitterId', result.user.providerData[0].uid);
          params.append('twitterName', result._tokenResponse.displayName);
          params.append('twitterIconUrl', result._tokenResponse.photoUrl);
          params.append('twitterCode', result._tokenResponse.screenName);
          params.append('twitterToken', result._tokenResponse.oauthAccessToken);
          params.append('twitterTokenSecret', result._tokenResponse.oauthTokenSecret);

          let loginParam = localStorage.getItem("login_param");
          localStorage.setItem("login_param", "");

          let method = "";
          if (loginParam == "LOGIN") {
            //ログイン処理
            method = "t_login";
          } else {
            //新規登録
            method = "registerTwitter";
          }

          axios.get(this.$root.CSRF_URL).then(response => {
            this.$root.log(response);
            axios.post(this.$root.SERVER_PATH + method, params)
            .then((response) => {
              let resData = response.data;
              if (method == "t_login") {
                //ログイン
                if (resData["status"] == this.$root.API_FAILED_NODATA) {
                  //データなし
                  localStorage.setItem("TWITTER", "NODATA");
                  this.$router.push("/login");
                  return;
                }

                if (resData["status"] == this.$root.API_FAILED_LOGIN) {
                  //IDとPASSでログイン失敗
                  localStorage.setItem("TWITTER", "LOGIN");
                  this.$router.push("/login");
                  return;
                }

                if (!this.$root.checkServerResponse(response, false)) {
                  return;
                }

                //成功時
                this.$root.isLogin = true;
                this.$router.push('/mainmenu');
              } else {
                //新規登録
                if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
                  //データなし
                  localStorage.setItem("TWITTER", "DUPULICATE");
                  this.$router.push("/registrationTwitter");
                  return;
                }

                if (resData["status"] == this.$root.API_FAILED_LOGIN) {
                  //IDとPASSでログイン失敗
                  localStorage.setItem("TWITTER", "LOGIN");
                  this.$router.push("/registrationTwitter");
                  return;
                }

                if (!this.$root.checkServerResponse(response, false)) {
                  return;
                }

                this.$root.isLogin = true;
                this.$router.push('/mainmenu');
              }
            })
            .catch((error) => {
              this.$root.comNetworkError(error);
            });
          });
        });

        getAuth().onAuthStateChanged((user) => {
          if (!user) {
            console.log("not login");
            //ログイン失敗時
            let loginParam = localStorage.getItem("login_param");
            localStorage.setItem("login_param", "");
            if (loginParam == "LOGIN") {
              this.$router.push("/login");
            } else if (loginParam == "NEW") {
              this.$router.push("/registrationTwitter");
            } else {
              this.$router.push("/");
            }
          }
        });
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log("ooooooooooooooooooooo1");
      vm.initAction();
