createApp(App)
.component("post-list-row", {
    props:["rowData"],
  template : `
    <div v-show="rowData">
        <div style="display:flex">
            <div v-show="rowData.listType == 'listTypeA' || this.rowData.listType == 'listTypeD' || this.rowData.listType == 'listTypeE'">
                <img :src="this.$root.ICON_PATH + rowData.userId + '.png'" v-on:click="pushReading" style="width:53px; height:53px; border-radius:50%">
            </div>
            <div style="width:100%; text-align:left; margin-left:10px">
                <span v-show="this.rowData.series != '指定なし'" class="post-user cursor" v-on:click="pushSeries">{{this.rowData.series}}</span>
                <div class="sp_line_0" />
                <span class="post-title cursor" v-on:click="pushReading">{{rowData.title}}</span>
                <div class="sp_line_0" />
                <span class="post-outline cursor" v-on:click="pushAuthor">作家名:{{this.rowData.author}}</span>
                <div class="sp_line_0" />
                <span class="post-outline">{{rowData.outline}}</span>
                <div class="sp_line_0" />
                <span v-for="(tag, key) in rowData.tags">
                    <router-link to="#" class="link" :class="this.$root.theme_fc_1" v-on:click.prevent.stop="pushTag(tag)">#{{tag}}</router-link>&nbsp;
                </span>

                <div class="sp_line_s" />

                <span class="post-sub">{{creationType}}&nbsp;{{ratingType}}{{charaType}}&nbsp;{{rowData.length}}文字
                <span v-show="rowData.listType != 'listTypeD'">&nbsp;{{publishingType}}</span></span><br>
                <div v-show="rowData.listType == 'listTypeB'">
                    <div style="height:6px" />
                    <img :src="this.$root.IMG_PATH + 'icon_post_stamp.png'" style="width:14px; height:12px">&nbsp;<span>{{rowData.stamp}}</span>&nbsp;&nbsp;
                    <img :src="this.$root.IMG_PATH + 'icon_post_bookmark.png'" style="width:11px; height:13px">&nbsp;<span>{{rowData.bookmark}}</span>&nbsp;&nbsp;
                    <img :src="this.$root.IMG_PATH + 'icon_post_view.png'" style="width:17px;">&nbsp;<span>{{rowData.view}}</span><br>
                    <div style="height:7px" />
                    <router-link class="btn btn-gray btn-icon btn-icon-edit" :to="'/edit/' + rowData.postId" style="width:130px">
                        作品を編集
                    </router-link>
                </div>

                <div class="sp_line_s" />

                <div class="post-sub" style="text-align:right">
                    投稿日:{{postDate}}
                </div>
            </div>
        </div>
        <div class="grayBar" />
    </div>
    `,
	data: function () {
			return {
                isMyPost: false
			}
	},
	computed : {
        test : function () {
            if (this.rowData == null) {
                this.$root.log("NULL");
            }
            return "";
        },
        editOutline : function () {
            return this.rowData.outline.replace(/\r?\n/g, ' ');
        },
        postDate : function () {
            var date = this.rowData.postDate;
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var day = date.getDate();
            var hour = date.getHours();
            var min = date.getMinutes();
            return year + "/" + month + "/" + day + " " + hour + ":" + min;
        },
        rowCol : function () {
            if (this.rowData.listType == 'listTypeA' || this.rowData.listType == 'listTypeD' || this.rowData.listType == 'listTypeE') {
                return  "col-9";
            } else {
                return "col-12";
            }
        },
        iconUrl : function () {
            return this.$root.ICON_PATH + this.rowData.userId + ".png";
        },
		author : function () {
			return this.rowData.author + "/" + this.rowData.series;
		},
		creationType : function () {
            if (this.rowData.creation == "0") {
                return "一次創作";
            } else {
                return "二次創作";
            }
		},
		ratingType : function () {
            if (this.rowData.rating == "0") {
                return "全年齢";
            } else if (this.rowData.rating == "1") {
                return "R-18";
            } else if (this.rowData.rating == "2") {
                return "R-18G";
            } else if (this.rowData.rating == "3") {
                return "PG12";
            }
		},
		charaType : function () {
            if (this.rowData.chara == "0") {
                return " 女主人公";
            } else if (this.rowData.chara == "1") {
                return " 男主人公";
            } else if (this.rowData.chara == "2") {
                return "";
            }
		},
        publishingType : function () {
            if (this.rowData.publishing == "0") {
                return "全体公開";
            } else if (this.rowData.publishing == "1") {
                return "パスワード";
            } else if (this.rowData.publishing == "2") {
                return "Twitter相互フォロワー限定";
            } else if (this.rowData.publishing == "3") {
                return "Twitterフォロワー限定";
            } else if (this.rowData.publishing == "4") {
                return "Twitterリスト限定";
            } else if (this.rowData.publishing == "99") {
                return "非公開";
            }
        }
	},
	methods : {
        pushSeries : function () {
            this.$router.push("/userpage/" + this.rowData.userId + "?page=1&series=" + this.rowData.series);
        },
        pushReading : function () {
            if (this.rowData.listType != "listTypeD") {
                this.$router.push("/reading/" + this.rowData.postId);
            }
        },
        pushAuthor : function () {
            this.$router.push("/userpage/" + this.rowData.userId);
        },
        getCurrentPage(currentPage) {
            this.$set(this, "currentPage", currentPage);
            //APIを呼び直す
            this.search(this.keyword, this.currentPage);
        },
        pushTag : function (tag) {
            //検索かそうでないかで分岐
            let curQuery = Object.assign({}, this.$route.query);
            if (curQuery["fil"] == null) {
                this.$router.push({path : "/search", query : {keyword : "#" + tag}});
            } else {
                //検索中
                curQuery["keyword"] = "#" + tag;
                curQuery["page"] = 1;
                this.$router.push({path : "/search", query : curQuery});
            }
        },
		pushLink (rIndex) {
            this.$root.log(rIndex);
			// this.$emit("pushRow", { rIndex: rIndex});
			// alert(this.$root.menu_check);
			// alert(this.$parent.rowKbn);
		}
	}
})
.component("user-list-row", {
    props:["rowData"],
    template : `
    <div class="row" v-if="rowData">
        <div class="col-3">
            <router-link :to="'/userpage/' + rowData.userId">
                <img v-bind:src="iconUrl" style="width:60px; height:60px;">
            </router-link>
        </div>
        <div class="col-9">
            <span class="p12">{{rowData.userName}}</span><br>
            <span class="p10">{{rowData.profile}}</span><br>
        </div>
        <div class="grayBar" />
    </div>
    `,
    data: function () {
            return {
                isMyPost: false
            }
    },
    computed : {
        iconUrl : function () {
            return this.$root.ICON_PATH + this.rowData.userId + ".png";
        }
    },
    methods : {
        pushLink (rIndex) {
            this.$root.log(rIndex);
            // this.$emit("pushRow", { rIndex: rIndex});
            // alert(this.$root.menu_check);
            // alert(this.$parent.rowKbn);
        }
    }
})
.component("ad-area", {
  template : `
    <div class="side">
        <img v-show="this.$root.adInfoArray.length != 0" :src="this.$root.IMG_PATH + 'ads/' + this.$root.adInfoArray[this.$root.adIndex]['file']" style="width:300px; height:250px" v-on:click="pushAd">
        <br>
        <br>
        <div style="background-color:#B4B4B4; width:300px; height:150px">
        </div>
        <br>
        <div style="background-color:#B4B4B4; width:300px; height:150px">
        </div>
        <br>
        <div style="background-color:#F28280; width:300px; height:100px">
        </div>
        <br>
        <div style="background-color:#F28280; width:300px; height:100px">
        </div>
        <br>
        <br>
    </div>
    `,
	data: function () {
			return {
			}
	},
	computed : {
	},
	methods : {
        pushAd : function() {
            window.open(this.$root.adInfoArray[this.$root.adIndex]['url']);
        }
	}
})
.component("popup-base", {
    props:["kbn", "params"],
    template : `
    <div id="overlay" v-show="isShowModal" v-on:click="closeModal(false)">
        <div id="content" style="text-align:center">
            <div class="row">
                <div class="col-2">
                </div>
                <div class="col-8" style="text-align:center">
                    <span class="p14">{{ params.title }}</span>
                </div>
                <div class="col-2" style="text-align:right">
                    <img v-show="kbn == 'COMMON' || kbn == 'DELETE_COMP'" class="imgBtn" v-on:click="pushAction(kbn)" :src="this.$root.IMG_PATH + 'btn_close.png'" style="width:14px; height:14px">
                </div>
            </div>
            <div class="grayBar" />
            <div v-show="params.body != ''">
                <div style="text-align:center; white-space: pre-line;" class="p14">{{ params.body }}</div>
                <div class="grayBar" />
            </div>
            <div v-show="kbn == 'MUTE_USER'">
                <div v-for="(name, key) in params.muteUserList" :key="key">
                    <p v-on:click="pushMute(key)" class="link">{{name}}</p>
                    <div style="height:16px" />
                </div>
            </div>
            <div v-if="kbn == 'POST_DELETE'">
                <!-- 投稿削除 -->
                <span class="btn" :class="this.$root.theme_bc_1" v-on:click="pushAction('POST_DELETE')" style="width:145px; margin-right:5px" >作品を削除</span>
                <span class="btn btn-gray" v-on:click="closeModal(true)" style="width:145px; margin-left:5px">キャンセル</span>
            </div>
            <div v-if="kbn == 'POST_DELETE_COMP'">
                <!-- 削除完了 -->
                <span class="btn" :class="this.$root.theme_bc_1" v-on:click="pushAction('POST_DELETE_COMP')" style="width:145px; margin:auto" >一覧に戻る</span>
            </div>
            <div v-if="kbn == 'WITHDRAWAL'">
                <!-- 退会 -->
                <span class="btn" :class="this.$root.theme_bc_1" v-on:click="pushAction('WITHDRAWAL')" style="width:145px; margin-right:5px" >退会する</span>
                <span class="btn btn-gray" v-on:click="closeModal(true)" style="width:145px; margin-left:5px">キャンセル</span>
            </div>
            <div v-if="kbn == 'POST_COMP'">
                <!-- 投稿完了 -->
                <span class="btn" :class="this.$root.theme_bc_1" v-on:click="pushAction('POST_COMP')" style="width:145px; margin-right:5px" >一覧に戻る</span>
                <span class="btn" :class="this.$root.theme_bc_1" style="width:144px" v-on:click="pushAction('POST_SHARE')">Twitterにシェア</span>
            </div>
            <div v-if="kbn == 'DELETE_CERTIFICATION'">
                <!-- 認証解除確認 -->
                <span class="btn" :class="this.$root.theme_bc_1" v-on:click="pushAction('DELETE_CERTIFICATION')" style="width:145px; margin-right:5px" >解除する</span>
                <span class="btn btn-gray" v-on:click="closeModal(true)" style="width:145px; margin-left:5px">キャンセル</span>
            </div>
            <div v-if="kbn == 'UPDATE' || kbn == 'MUTE_USER' || kbn == 'CERTIFICATION' || kbn == 'STAMP' || kbn == 'READING_PUBLISH' || kbn == 'POST_PUBLISH' || kbn == 'COMMON'">
                <!-- 変更 -->
                <!-- ミュートユーザー -->
                <!-- 認証メール発行 -->
                <!-- スタンプ上限 -->
                <!-- READINGの公開方法エラー -->
                <!-- 投稿時の公開方法 -->
                <!-- 共通 -->
                <span class="btn" :class="this.$root.theme_bc_1" v-on:click="pushAction(kbn)" style="width:145px; margin:auto" >閉じる</span>
            </div>
        </div>
    </div>
    `,
    data: function () {
        return {
            isShowModal: false,
        }
    },
    computed : {
    },
    methods : {
        pushMute : function(userId) {
            document.getElementById('body').className = "";
            this.isShowModal = false;    
            this.$router.push('/userpage/' + userId);
        },
        pushAction : function(val) {
            if (val != "POST_SHARE") {
                this.closeModal(true);
            }
            this.$emit('popupAction', val);
        },
        showModal : function() {
            document.getElementById('body').className = "modalBg";
            this.isShowModal = true;
        },
        closeModal: function(isCloseBtn) {
            this.$root.log(isCloseBtn);
            if (isCloseBtn == false &&
                 (this.kbn == "DELETE_COMP" || this.kbn == "UPDATE" || this.kbn == "POST_COMP" || this.kbn == "CERTIFICATION" || this.kbn == "POST_PUBLISH" || this.kbn == "READING_PUBLISH")) {
                return;
            }
            document.getElementById('body').className = "";
            this.isShowModal = false;    
        },
    }
})
.use(router, VueAxios, axios)
.use(router, axios)
.mount('#app')
