<template>
  <div class="latestlist container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <div class="k_ti">
            新着一覧
          </div>

          <div class="grayBar" />

          <post-list-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></post-list-row>
          <subPagenation
            :showPages="showPages"
            :currentPage="currentPage"
            :totalCount="totalCount"
            :perPage="perPage"
            :totalPages="totalPages"
            @currentPage="getCurrentPage"
          ></subPagenation>
        </div>
        <br>
      </div>
      <ad-area></ad-area>
    </div>
  </div>
</template>

<script>
import SubPagenation from './SubPagenation.vue';
import axios from 'axios'

export default {
  name: 'LatestList',
  components: {
    SubPagenation
  },
  data() {
    return {
      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 10, //1ページの表示件数
      totalCount: 15, //取得したアイテムの総件数
      totalPages: 3, //総ページ数

      allPostDataList: null,
      dispDataList: null,
    };
  },
  computed : {
  },
  methods : {
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    getLatestPostList : function () {
      //パラメータチェック
      let query = Object.assign({}, this.$route.query);
      let page = query["page"];

      //ページが存在しないまたはマイナス
      if (page == null || page < 0) {
        console.log("やりなおし");
        query["page"] = 1;
        this.$router.replace({query});
        return;
      }

      this.currentPage = page;

      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + 'getLatestPostList', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        let resData = response.data;
        let resDataList = resData["postList"];
        this.allPostDataList = [];
        for (let i = 0; i < resDataList.length; i++) {
          let rowData = resDataList[i];

          var tagStr = rowData["tags"];
          var tagArray = {};
          if (tagStr != "") {
            tagArray = tagStr.split(',');
          }

          var data = {
            postId : rowData["post_id"],
            userId : rowData["user_id"],
            listType : "listTypeE",
            author : rowData["user_name"],
            series : rowData["series"],
            title : rowData["title"],
            outline : rowData["outline"],
            rating : rowData["rating"],
            chara : rowData["chara"],
            creation : rowData["creation"],
            tags : tagArray,
            length : rowData["length"],
            postDate : new Date(rowData["created_at"])
          };
          this.allPostDataList.push(data);
        }

        this.totalCount = resData["count"];

        var page = Math.floor(this.totalCount / 10);
        if (this.totalCount % 10 != 0) {
          page = page + 1;
        }
        this.totalPages = page;
        console.log("INaaa");
        //初期処理
        this.initAction();

      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    initAction : function() {

      //ページ数
      this.totalCount = this.allPostDataList.length;
      var tPage = Math.floor(this.totalCount / 10);
      if (this.totalCount % 10 != 0) {
        tPage = tPage + 1;
      }
      this.totalPages = tPage;

      //ページ数が最大を超えている場合
      if (this.totalPages < this.currentPage) {
        let query = Object.assign({}, this.$route.query);
        query["page"] = this.totalPages;
        this.$router.replace({query});
      }

      //ページを表示
      this.dispDataList = this.$root.retComPagingList(this.currentPage, this.allPostDataList);

      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getLatestPostList();
      vm.$root.setCurrentPage("LATEST");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getLatestPostList();
      vm.$root.setCurrentPage("LATEST");
    });
  },
  watch : {
    '$route'() {
      console.log("aaa " + this.$route.name);
      if (this.$route.name == "LatestList") {
        this.$root.showLoad();
        this.getLatestPostList();
      }
    }
  }
}
</script>

