<template>
  <div class="registrationConfirmation" style="text-align:center; width:270px; margin:auto">
    <div style="height:36px" />
    <span class="p12">{{ title }}</span>
    <div style="height:20px" />
    <span class="p9">{{ message }}</span>
    <div style="height:38px" />
    <span class="btn" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="this.$root.pushHome">トップへ</span>
    <br>
    <br>
    <br>
    <br>
  </div>
</template>

<script>
import { getAuth, signOut } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'RegistrationConfirmation',
  components: {
  },
  data() {
    return {
      title : "",
      message : "",
    };
  },
  computed : {
  },
  methods : {
    initAction : function () {
      let tempId = this.$route.params.tempId;
      
      if (tempId == null || tempId.length != 20) {
        this.title = "本登録エラー";
        this.message = "このURLは無効なパラメータが含まれているか、有効期限が切れています";
        this.$root.closePopup();
        return;
      }

      let params = new URLSearchParams();
      params.append('tempId', tempId);

      axios.post(this.$root.SERVER_PATH + 'register', params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);

        if (resData["status"] == this.$root.API_FAILED_NODATA || resData["status"] == this.$root.API_FAILED_PARAM) {
          //該当データなし
          this.title = "本登録エラー";
          this.message = "このURLは無効なパラメータが含まれているか、有効期限が切れています";
        } else if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
          //使用済みメールアドレス
          this.title = "本登録エラー";
          this.message = "ご登録いただいたメールアドレスは既に使用されています";
        } else if (resData["status"] == this.$root.API_FAILED_EXEPTION) {
          //本登録エラー
          this.title = "本登録エラー";
          this.message = "本登録処理でエラーが発生しました";
        } else {
          console.log("AAAAAAAAAA");

          if (resData["kbn"] == "NEW") {
            this.title = "本登録完了";
            this.message = "本登録が完了しました\nトップページからログインを行ってください";
          } else if (resData["kbn"] == "ADD") {
            this.title = "メールアドレス認証完了";
            this.message = "メールアドレス認証が完了しました\nトップページからログインを行ってください";
            this.$root.closePopup();
          } else if (resData["kbn"] == "CHANGE") {
            this.title = "メールアドレス変更完了";
            this.message = "メールアドレスの変更が完了しました\nトップページからログインを行ってください";
          }

          //ログアウト処理
          signOut(getAuth());
          this.$root.userData = {
            userId : "",
            userName : "",
            profile : "",
          };

          this.$root.isLogin = false;
        }
        
        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.initAction();
      vm.$root.setCurrentPage("");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.initAction();
      vm.$root.setCurrentPage("");
    });
  }
}
</script>

