<script>
import axios from 'axios'

export default {
  name: 'Edit',
  components: {
  },
  data() {
    return {
      edPostId : "",
      edTitle : "",
      edOutline : "",
      edBody : "",
      edConversionList : [["",""], ["",""], ["",""]],
      edSeries : "",
      edSelectSeries : "",
      edRatingIndex : 0,
      edCharacterIndex : 0,
      edCreationIndex : 0,
      edTags : "",
      edFilter : 0,
      edPublishing : 0,
      edPublishingPassword : "",
      edPublishingHint : "",
      edSelectPublishingList : "",
      edSearchable : true,

      edIsNew : false,
      edSeriesList : [],
      edTwitterList : [],

      edErrorMessageData : {
        title : "",
        outline : "",
        body : "",
        conversion : "",
        series : "",
        tags : "",
        publishing : "",
        api : "",
      },

      popupKbn : "",
      popupParams : {
        title : "",
        body : "",
        tweet : "",
      },

      isGetTwitterList : false,
    };
  },
  computed : {
    btnStyleRating0 : function() {
      if (this.edRatingIndex == 0) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    btnStyleRating1 : function() {
      if (this.edRatingIndex == 1) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    btnStyleRating2 : function() {
      if (this.edRatingIndex == 2) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    btnStyleRating3 : function() {
      if (this.edRatingIndex == 3) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    btnStyleCharacter0 : function() {
      if (this.edCharacterIndex == 0) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    btnStyleCharacter1 : function() {
      if (this.edCharacterIndex == 1) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    btnStyleCharacter2 : function() {
      if (this.edCharacterIndex == 2) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    btnStyleCreation0 : function() {
      if (this.edCreationIndex == 0) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    btnStyleCreation1 : function() {
      if (this.edCreationIndex == 1) { return this.$root.theme_bc_1; } else {  return "btn-sq-gray"; }
    },
    lenTitle : function() {
      return this.edTitle.length;
    },
    lenOutline : function() {
      return this.edOutline.length;
    },
    lenBody : function() {
      var len = this.edBody.length;
      return len.toLocaleString();
    },
    theme_btn_icon_plus : function() {
      if (this.$root.themeIndex == 0) {
          return "btn-icon-plus_1";
        } else if (this.$root.themeIndex == 1) {
          return "btn-icon-plus_2";
        } else if (this.$root.themeIndex == 2) {
          return "btn-icon-plus_3";
        } else if (this.$root.themeIndex == 3) {
          return "btn-icon-plus_4";
        }
        return "btn_plus_icon_1";
    },
    theme_switch : function() {
        if (this.$root.themeIndex == 0) {
          return "theme_switch_1";
        } else if (this.$root.themeIndex == 1) {
          return "theme_switch_2";
        } else if (this.$root.themeIndex == 2) {
          return "theme_switch_3";
        } else if (this.$root.themeIndex == 3) {
          return "theme_switch_4";
        }
        return "theme_switch_1";
    }
  },
  methods : {
    test : function() {
    },
    deleteConversion : function(index) {
      this.edConversionList.splice(index, 1);
    },
    addConversion : function () {
      if (this.edConversionList.length < 4) {
        this.edConversionList.push(["", ""]);
      }
    },
    pushEdRating : function (val) {
      this.edRatingIndex = val;
      this.chValue('RATING');
    },
    pushEdCharacter : function (val) {
      this.edCharacterIndex = val;
      this.chValue('CHARACTER');
    },
    pushEdCreation : function (val) {
      this.edCreationIndex = val;
      this.chValue('CREATION');
    },
    PushBtn : function(name) {
      if (name == "NEW" || name == "UPDATE") {
        this.$root.showLoad();

        //入力チェック
        this.edErrorMessageData.title = "";
        this.edErrorMessageData.outline = "";
        this.edErrorMessageData.body = "";
        this.edErrorMessageData.conversion = "";
        this.edErrorMessageData.series = "";
        this.edErrorMessageData.tags = "";
        this.edErrorMessageData.api = "";

        //トリム
        this.edTitle = this.edTitle.trim();
        this.edOutline = this.edOutline.trim();
        this.edBody = this.edBody.trim();
        
        
        //入力チェック
        if (this.edTitle == "") {
          this.edErrorMessageData.title = "タイトルは1文字以上入力してください";
        } else if (this.edTitle.length > 50) {
          this.edErrorMessageData.title = "タイトルは50文字以下で入力してください";
        } else {
          let regStr = this.$root.checkRegulation(this.edTitle)  
          if (regStr != "") {
            this.edErrorMessageData.title = this.$root.STR_ERROR_REGULATION + regStr;
          }
        }
        
        if (this.edOutline == "") {
          this.edErrorMessageData.outline = "あらすじは1文字以上入力してください";
        } else if (this.edOutline.length > 200) {
          this.edErrorMessageData.outline = "あらすじは200文字以下で入力してください";
        } else {
          let regStr = this.$root.checkRegulation(this.edOutline)  
          if (regStr != "") {
            this.edErrorMessageData.outline = this.$root.STR_ERROR_REGULATION + regStr;
          }
        }
        
        if (this.edBody == "") {
          this.edErrorMessageData.body = "本文は1文字以上入力してください";
        } else if (this.edBody.length > 20000) {
          this.edErrorMessageData.body = "本文は20000文字以下で入力してください";
        } else {
          let regStr = this.$root.checkRegulation(this.edBody)  
          if (regStr != "") {
            this.edErrorMessageData.body = this.$root.STR_ERROR_REGULATION + regStr;
          }
        }

        if (this.edErrorMessageData.title != "" || this.edErrorMessageData.outline != "" || this.edErrorMessageData.body != "") {
          this.$root.closePopup();
          return;
        }
        
        //変換項目成型
        var conversionStr = "";
        for (let i = 0; i < this.edConversionList.length; i++) {
          const element = this.edConversionList[i];
          let val1 = element[0].trim();
          let val2 = element[1].trim();
          if (val1 != "" && val2 != "") {
            conversionStr += val1 + this.$root.CONVERSION1 + val2 + this.$root.CONVERSION2;
          } else if (val1 == "" && val2 != "") {
            //片方だけ空文字
            this.edErrorMessageData.conversion = "項目説明は1文字以上入力してください";
            this.$root.closePopup();
            return;
          } else if (val1 != "" && val2 == "") {
            //片方だけ空文字
            this.edErrorMessageData.conversion = "変換文字は1文字以上入力してください";
            this.$root.closePopup();
            return;
          } else if (val1.length > 20) {
            this.edErrorMessageData.conversion = "項目説明は20文字以下で入力してください";
            this.$root.closePopup();
            return;
          } else if (val2.length > 20) {
            this.edErrorMessageData.conversion = "変換文字は10文字以下で入力してください";
            this.$root.closePopup();
            return;
          } else {
            let regStr1 = this.$root.checkRegulation(val1);  
            if (regStr1 != "") {
              this.edErrorMessageData.conversion = this.$root.STR_ERROR_REGULATION + regStr1;
              this.$root.closePopup();
              return;
            }
            let regStr2 = this.$root.checkRegulation(val2); 
            if (regStr2 != "") {
              this.edErrorMessageData.conversion = this.$root.STR_ERROR_REGULATION + regStr2;
              this.$root.closePopup();
              return;
            }
          }
        }

        //最後のカンマを除去
        if (conversionStr != "") {
          conversionStr = conversionStr.substr(0, conversionStr.length - this.$root.CONVERSION2.length);
        }

        //シリーズ選択
        //トリム
        this.edSeries = this.edSeries.trim();
        
        var seriesStr = this.edSeries;
        if (seriesStr == "") {
          seriesStr = this.edSelectSeries;
        }

        //入力チェック
        if (seriesStr.length > 50) {
          this.edErrorMessageData.series = "シリーズが50文字を超えています";
          this.$root.closePopup();
          return;
        } else {
          let regStr = this.$root.checkRegulation(seriesStr)  
          if (regStr != "") {
            this.edErrorMessageData.series = this.$root.STR_ERROR_REGULATION + regStr;
          }
        }

        //ジャンルタグ
        this.edTags = this.edTags.trim();
        //#がついていれば除去
        this.edTags = this.$root.arrangeStr(this.edTags, ',');
        
        let tagArray = this.edTags.split(',');
        if (tagArray.length > 8) {
          this.setErrorMessageData.tags = "ジャンルタグは8個まで登録できます";
          this.$root.closePopup();
          return;
        }
        for (let i = 0; i < tagArray.length; i++) {
          const element = tagArray[i];
          if (element.length > 21) {
            this.setErrorMessageData.tags = "ジャンルタグは1つあたり20文字以下で入力してください";
            this.$root.closePopup();
            return;
          } else {
            let regStr = this.$root.checkRegulation(element)  
            if (regStr != "") {
              this.edErrorMessageData.tags = this.$root.STR_ERROR_REGULATION + regStr;
            }
          }
        }

        //異常値チェック
        if ((this.edRatingIndex != 0 && this.edRatingIndex != 1 && this.edRatingIndex != 2 && this.edRatingIndex != 3) ||
         (this.edCharacterIndex != 0 && this.edCharacterIndex != 1 && this.edCharacterIndex != 2) || 
         (this.edCreationIndex != 0 && this.edCreationIndex != 1) || 
         (this.edFilter != 0 && this.edFilter != 1 && this.edFilter != 2 && this.edFilter != 3 && this.edFilter != 4 && this.edFilter != 5) || 
         (this.edPublishing != 0 && this.edPublishing != 1 && this.edPublishing != 2 && this.edPublishing != 3 && this.edPublishing != 4 && this.edPublishing != 99) || 
         (this.edSearchable != false && this.edSearchable != true)) {
          //エラー
          this.$router.push({path : "/error", query : {errorCode : this.$root.API_FAILED_PARAM, message :""}});
          this.$root.closePopup();
          return;
        }

        //公開方法別チェック
        let publishingSub1 = "";
        let publishingSub2 = "";
        if (this.edPublishing == 1) {
          //パスワード
          if (this.edPublishingPassword == "") {
            this.edErrorMessageData.publishing = "パスワードは1文字以上入力してください";
            this.$root.closePopup();
            return;
          } else if (this.edPublishingPassword.length > 20) {
            this.edErrorMessageData.publishing = "パスワードは20文字以下で入力してください";
            this.$root.closePopup();
            return;
          }

          if (this.edPublishingHint == "") {
            this.edErrorMessageData.publishing = "ヒントは1文字以上入力してください";
            this.$root.closePopup();
            return;
          } else if (this.edPublishingHint.length > 50) {
            this.edErrorMessageData.publishing = "ヒントは50文字以下で入力してください";
            this.$root.closePopup();
            return;
          }
          publishingSub1 = this.edPublishingPassword;
          publishingSub2 = this.edPublishingHint;
        } else if (this.edPublishing == 4) {
          //twitterリスト限定
          if (this.edTwitterList.length == 0) {
            this.edErrorMessageData.publishing = "twitterリストがありません";
            this.$root.closePopup();
            return;
          } else if (this.edPublishingListIndex == 0) {
            this.edErrorMessageData.publishing = "twitterリストを選択してください";
            this.$root.closePopup();
            return;
          }
          publishingSub1 = this.edSelectPublishingList;
        }

        let params = new URLSearchParams();
        let method = "";
        let pBody = "";
        if (name == "NEW") {
          method = "insertPost";
          params.append('postId', "");
          pBody = "作品の投稿が完了しました";
        } else if (name == "UPDATE") {
          method = "updatePost";
          params.append('postId', this.edPostId);
          pBody = "作品の更新が完了しました";
        }
        
        params.append('title', this.edTitle);
        params.append('outline', this.edOutline);
        params.append('body', this.edBody);
        params.append('conversion', conversionStr);
        params.append('series', seriesStr);
        params.append('rating', this.edRatingIndex);
        params.append('chara', this.edCharacterIndex);
        params.append('creation', this.edCreationIndex);
        params.append('tags', this.edTags);
        params.append('filter', this.edFilter);
        params.append('publishing', this.edPublishing);
        params.append('publishingSub1', publishingSub1);
        params.append('publishingSub2', publishingSub2);
        params.append('searchable', this.edSearchable);

        axios.post(this.$root.SERVER_PATH + method, params)
        .then((response) => {
          
          let resData = response.data;
          if (resData["status"] == this.$root.API_FAILED_NODATA) {
            //データなし
            this.$router.replace({path : "/error", query : {errorCode : resData["status"], message : "選択した投稿は存在しません"}});
            return;
          }
          
          if (!this.$root.checkServerResponse(response, true)) {
            return;
          }

          if (name == "NEW") {
            //新規の場合投稿IDを保存（SNS用）
            this.edPostId = resData["postId"];
          }

          //下書きデータを削除
          this.setDefaultDraft();

          this.popupKbn = "POST_COMP";
          this.popupParams = {
            title : "",
            body : pBody,
          }

          this.$root.closePopup();
          this.$refs.popup.showModal();
        }).catch((error) => {
          this.$root.comNetworkError(error);
        });
      }
    },
    chPublishing : function() {
      this.chValue('PUBLISHING');

      if (this.$root.userData.isAuthTwitter == false && (this.edPublishing == 2 || this.edPublishing == 3 || this.edPublishing == 4)) {
        //twitter認証していないエラー
        this.popupKbn = "POST_PUBLISH";
        this.popupParams = {
          title : "Twitter認証",
          body : "この項目を設定するにはTwitter連携が必要です",
        }
        this.$refs.popup.showModal();
        return;
      } else if (this.edPublishing == 1) {
        //パスワード
        this.edPublishingPassword = "";
        this.edPublishingHint = "";
      } else if (this.edPublishing == 4 && this.isGetTwitterList == false) {
        //Twitterリスト未取得
        this.edSelectPublishingList = "";
        this.getTwitterList(false);
      }
    },
    getTwitterList : function (initFlg) {
      if (!initFlg) {
        this.$root.showLoad();
      }

      let params = new URLSearchParams();
      params.append('kbn', "TWITTER_LIST");  

      axios.post(this.$root.SERVER_PATH + "getTwitterInfo", params)
      .then((response) => {
        let resData = response.data;
        let twitterList = resData["twitterList"];
        this.edTwitterList = [];
        this.$root.log(resData);
        if (twitterList["status"] == '1') {
          this.edTwitterList = [["", "リストを選択してください"]];
          let list = twitterList["list"];
          for (let i = 0; i < list.length; i++) {
            let data = list[i];

            this.$root.log(data["id"] + " " + data["name"]);
            this.edTwitterList.push([data["id"], data["name"]]);            
          }
        } else if (twitterList["status"] == '0') {
          //twitter取得上限
          this.popupKbn = "POST_PUBLISH";
          this.popupParams = {
          title : this.$root.STR_TITLE_TWITTER_API_LIMIT,
          body : this.$root.STR_BODY_TWITTER_API_LIMIT,
          }
          if (!initFlg) {
            this.$root.closePopup();
          }
          this.$refs.popup.showModal();
        }
        this.isGetTwitterList = true;
        if (!initFlg) {
          this.$root.closePopup();
        }
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    popupAction: function(val) {
      if (val == "POST_DELETE") {
        // 投稿削除
        this.$root.showLoad();

        let params = new URLSearchParams();
        params.append('postId', this.edPostId);

        axios.post(this.$root.SERVER_PATH + 'deletePost', params)
        .then((response) => {
          if (!this.$root.checkServerResponse(response, true)) {
            return;
          }
          this.popupKbn = "POST_DELETE_COMP";
          this.popupParams = {
            title : "削除しました",
            body : "",
          }

          this.$root.closePopup();
          this.$refs.popup.showModal();
        });
      } else if (val == "POST_DELETE_COMP" || val == "POST_COMP") {
        this.$router.push("/postlist");
      } else if (val == "POST_SHARE") {
        var url = "https://twitter.com/intent/tweet?" + 
        "hashtags=" + encodeURIComponent('yumedrop,夢小説,テスト') + 
        "&text=" + encodeURIComponent("ユメドロップ作品ページだよ") + 
        "&url=" + encodeURIComponent(this.$root.BASE_URL + "reading/" + this.edPostId) + 
        "&via=" + encodeURIComponent("iphonetaro_live");
        window.open(url);
      }
    },
    getMySeriesList : function () {
      this.$root.showLoad();

      let params = new URLSearchParams();

      axios.post(this.$root.SERVER_PATH + 'getMySeriesList', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        let resData = response.data;

        let resDataList = resData["seriesList"];
        this.edSeriesList = [];
        for (let i = 0; i < resDataList.length; i++) {
          this.edSeriesList.push(resDataList[i]);
        }
        
        for (let i = 0; i < this.edSeriesList.length; i++) {
          if (this.edSeriesList[i] == this.edSeries) {
            this.edSelectSeries = this.edSeriesList[i];
            this.edSeries = "";
            break;
          }
        }

        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    pushDelete : function () {
      this.popupKbn = "POST_DELETE";
      this.popupParams = {
        title : "本当に削除しますか？",
        body : "一度削除されたものを\n元に戻すことはできません",
      }
      this.$refs.popup.showModal();
    },
    getPostData : function () {
      this.$root.showLoad();
      
      let postId = this.$route.params.postId;
      
      let params = new URLSearchParams();
      params.append('postId', postId);

      axios.post(this.$root.SERVER_PATH + 'getMyPostData', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          //データなし
          this.$router.replace({path : "/error", query : {errorCode : resData["status"], message : "選択した投稿は存在しません"}});
          return;
        }

        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        let rowData = resData["postData"];

        this.edIsNew = false;
        this.edPostId = rowData["post_id"];
        this.edTitle = rowData["title"];
        this.edOutline = rowData["outline"];
        this.edBody = rowData["body"];
        this.edConversionList = rowData.conversionList;
        this.edSeries = rowData["series"];
        this.edRatingIndex = rowData["rating"];
        this.edCharacterIndex = rowData["chara"];
        this.edCreationIndex = rowData["creation"];
        this.edTags = rowData["tags"];
        this.edFilter = rowData["filter"];
        this.edPublishing = rowData["publishing"];
        if (rowData["searchable"] == "1") {
          this.edSearchable = true;
        } else {
          this.edSearchable = false;
        }

        var conversionStr = rowData["conversion"];
        if (conversionStr == "") {
          this.edConversionList = [["",""], ["",""], ["",""]];
        } else {
          this.edConversionList = [];
          var cArray = conversionStr.split(this.$root.CONVERSION2);
          for (let i = 0; i < cArray.length; i++) {
            const element = cArray[i];
            var ccArray = element.split(this.$root.CONVERSION1);
            this.edConversionList.push([ccArray[0], ccArray[1]]);
          }
        }

        //公開方法処理
        if (this.edPublishing == "1") {
          //パスワード
          this.edPublishingPassword = rowData["publishing_sub1"];
          this.edPublishingHint = rowData["publishing_sub2"];
        } else if (this.edPublishing == "4") {
          //公開リスト
          this.edTwitterList = [["", "リストを選択してください"]];
          let twitterList = resData["twitterList"];
          let listId = rowData["publishing_sub1"];
          this.edSelectPublishingList = listId;
          if (twitterList["status"] == '1') {
            let list = twitterList["list"];
            for (let i = 0; i < list.length; i++) {
              let data = list[i];
              this.edTwitterList.push([data["id"], data["name"]]);            
            }
          } else if (twitterList["status"] == '0') {
            //twitter取得上限
            this.popupKbn = "POST_PUBLISH";
            this.popupParams = {
            title : this.$root.STR_TITLE_TWITTER_API_LIMIT,
            body : this.$root.STR_BODY_TWITTER_API_LIMIT,
            }

            this.$root.closePopup();
            this.$refs.popup.showModal();
            return;
          }
        }

        //シリーズ一覧編集
        let resDataList = resData["seriesList"];
        this.edSeriesList = [];
        for (let i = 0; i < resDataList.length; i++) {
          this.edSeriesList.push(resDataList[i]);
        }

        for (let i = 0; i < this.edSeriesList.length; i++) {
          if (this.edSeriesList[i] == this.edSeries) {
            this.edSelectSeries = this.edSeriesList[i];
            this.edSeries = "";
            break;
          }
        }

        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    handleKeydown(e) {
      this.chValue('OUTLINE')
      if (this.composing) return // IME対応(日本語変換中のEnterを止める)
      // const rowCount = this.edOutline.match(/\n/g).length
      // const rowCount = this.edOutline.split(/\r?\n/g).length;

      // this.$root.log(rowCount + " " + this.edOutline + " " + e.key);
      // if (rowCount >= 3 && e.key === 'Enter') {
        // テキストエリアからフォーカスを外すことで、Enterの連打を防ぐ
        // document.activeElement.blur();
      if (e.key === 'Enter') {
        e.preventDefault();
      }
    },
    setNewData : function () {
      //下書きデータがあるかどうか
      // localStorage.removeItem("DRAFT");
      let isDraft = localStorage.getItem("DRAFT_" + this.$root.userData.userId);      
      if (isDraft == null) {
        //下書きデータの初期化
        console.log("初期化");
        this.setDefaultDraft();
        isDraft = "";
      }

      this.edTitle = localStorage.getItem("D_TITLE_" + this.$root.userData.userId);
      this.edOutline = localStorage.getItem("D_OUTLINE_" + this.$root.userData.userId);
      this.edBody = localStorage.getItem("D_BODY_" + this.$root.userData.userId);
      let conversionStr = localStorage.getItem("D_CONVERSION_" + this.$root.userData.userId);
      let conversionArray = conversionStr.split(this.$root.CONVERSION2);
      this.edConversionList = [];
      for (let i = 0; i < conversionArray.length; i++) {
        const conversion = conversionArray[i].split(this.$root.CONVERSION1);
        this.edConversionList.push([this.$root.chNull(conversion[0]) , this.$root.chNull(conversion[1])]);
      }
      this.edSelectSeries = localStorage.getItem("D_SELECT_SERIES_" + this.$root.userData.userId);
      this.edSeries = localStorage.getItem("D_SERIES_" + this.$root.userData.userId);
      this.edRatingIndex = parseInt(localStorage.getItem("D_RATING_" + this.$root.userData.userId));
      this.edCharacterIndex = parseInt(localStorage.getItem("D_CHARACTER_" + this.$root.userData.userId));
      this.edCreationIndex = parseInt(localStorage.getItem("D_CREATION_" + this.$root.userData.userId));
      this.edTags = localStorage.getItem("D_TAGS_" + this.$root.userData.userId);
      this.edFilter = parseInt(localStorage.getItem("D_FILTER_" + this.$root.userData.userId));
      this.edPublishing = parseInt(localStorage.getItem("D_PUBLISHING_" + this.$root.userData.userId));
      this.edPublishingPassword = localStorage.getItem("D_P_PASSWORD_" + this.$root.userData.userId);
      this.edPublishingHint = localStorage.getItem("D_P_HINT_" + this.$root.userData.userId);
      this.edSelectPublishingList = localStorage.getItem("D_P_LIST_" + this.$root.userData.userId);
      
      if (this.edPublishing == 4) {
        //twitterのリスト取得
        this.getTwitterList(true);
        console.log(this.edSelectPublishingList);
      } else {
        this.edTwitterList = [];
        this.isGetTwitterList = false;
      }

      if (localStorage.getItem("D_SEARCHABLE_" + this.$root.userData.userId) == "1") {
        this.edSearchable = true;
      } else {
        this.edSearchable = false;
      }

      // console.log(isDraft);
      // if (isDraft == "") {
      //   this.edPublishing = 0;
      // } else {
      //   this.edPublishing = 99;
      // }

      this.edPostId = "";
      this.edIsNew = true;

      //シリーズ一覧取得
      this.getMySeriesList();
    },
    setDefaultDraft : function () {
      localStorage.setItem("DRAFT_" + this.$root.userData.userId, "");
      localStorage.setItem("D_TITLE_" + this.$root.userData.userId, "");
      localStorage.setItem("D_OUTLINE_" + this.$root.userData.userId, "");
      localStorage.setItem("D_BODY_" + this.$root.userData.userId, "");
      localStorage.setItem("D_CONVERSION_" + this.$root.userData.userId, this.$root.CONVERSION1 + this.$root.CONVERSION2 + this.$root.CONVERSION1 + this.$root.CONVERSION2 + this.$root.CONVERSION1);
      localStorage.setItem("D_SELECT_SERIES_" + this.$root.userData.userId, "指定なし");
      localStorage.setItem("D_SERIES_" + this.$root.userData.userId, "");
      localStorage.setItem("D_RATING_" + this.$root.userData.userId, "0");
      localStorage.setItem("D_CHARACTER_" + this.$root.userData.userId, "0");
      localStorage.setItem("D_CREATION_" + this.$root.userData.userId, "0");
      localStorage.setItem("D_TAGS_" + this.$root.userData.userId, "");
      localStorage.setItem("D_FILTER_" + this.$root.userData.userId, "0");
      localStorage.setItem("D_PUBLISHING_" + this.$root.userData.userId, "0");
      localStorage.setItem("D_P_PASSWORD_" + this.$root.userData.userId, "");
      localStorage.setItem("D_P_HINT_" + this.$root.userData.userId, "");
      localStorage.setItem("D_P_LIST_" + this.$root.userData.userId, "");
      localStorage.setItem("D_SEARCHABLE_" + this.$root.userData.userId, "1");
    },
    chValue : function (column) {
      console.log(column);
      localStorage.setItem("DRAFT_" + this.$root.userData.userId, "DRAFT");
      if (column == "TITLE") {
        localStorage.setItem("D_TITLE_" + this.$root.userData.userId, this.edTitle);
      } else if (column == "OUTLINE") {
        localStorage.setItem("D_OUTLINE_" + this.$root.userData.userId, this.edOutline);
      } else if (column == "BODY") {
        localStorage.setItem("D_BODY_" + this.$root.userData.userId, this.edBody);
      } else if (column == "CONVERSION") {
        let conversionStr = "";
        for (let i = 0; i < this.edConversionList.length; i++) {
          conversionStr = conversionStr + this.edConversionList[i][0] + this.$root.CONVERSION1 + this.edConversionList[i][1];
          if (i != this.edConversionList.length - 1) {
            conversionStr = conversionStr + this.$root.CONVERSION2;
          }
        }
        localStorage.setItem("D_CONVERSION_" + this.$root.userData.userId, conversionStr);
      } else if (column == "SELECT_SERIES") {
        localStorage.setItem("D_SELECT_SERIES_" + this.$root.userData.userId, this.edSelectSeries);
      } else if (column == "SERIES") {
        localStorage.setItem("D_SERIES_" + this.$root.userData.userId, this.edSeries);
      } else if (column == "CREATION") {
        localStorage.setItem("D_CREATION_" + this.$root.userData.userId, this.edCreationIndex);
      } else if (column == "CHARACTER") {
        localStorage.setItem("D_CHARACTER_" + this.$root.userData.userId, this.edCharacterIndex);
      } else if (column == "RATING") {
        localStorage.setItem("D_RATING_" + this.$root.userData.userId, this.edRatingIndex);
      } else if (column == "FILTER") {
        localStorage.setItem("D_FILTER_" + this.$root.userData.userId, this.edFilter);
      } else if (column == "TAGS") {
        localStorage.setItem("D_TAGS_" + this.$root.userData.userId, this.edTags);
      } else if (column == "PUBLISHING") {
        localStorage.setItem("D_PUBLISHING_" + this.$root.userData.userId, this.edPublishing);
        if (this.edPublishing != 1) {
          //パスとヒント
          localStorage.setItem("D_P_PASSWORD_" + this.$root.userData.userId, "");
          localStorage.setItem("D_P_HINT_" + this.$root.userData.userId, "");
        }
        if (this.edPublishing != 4) {
          //リスト
          localStorage.setItem("D_P_LIST_" + this.$root.userData.userId, "");
        }
      } else if (column == "P_PASSWORD") {
        localStorage.setItem("D_P_PASSWORD_" + this.$root.userData.userId, this.edPublishingPassword);
      } else if (column == "P_HINT") {
        localStorage.setItem("D_P_HINT_" + this.$root.userData.userId, this.edPublishingHint);
      } else if (column == "P_LIST") {
        localStorage.setItem("D_P_LIST_" + this.$root.userData.userId, this.edSelectPublishingList);
      } else if (column == "SEARCHABLE") {
        let val = "0";
        if (this.searchable == true) {
          val = "1";
        }
        localStorage.setItem("D_SEARCHABLE_" + this.$root.userData.userId, val);
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();

      var postId = vm.$route.params.postId;
      
      if (postId == "new") {
        //新規投稿
        vm.setNewData();
      } else {
        //編集
        vm.getPostData();
      }
      vm.$root.setCurrentPage("EDIT");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();

      var postId = vm.$route.params.postId;

      if (postId == "new") {
        //新規投稿
        vm.setNewData();
      } else {
        //編集
        vm.getPostData();
      }
      vm.$root.setCurrentPage("EDIT");
    });
  }
}
</script>

