<template>
  <div class="postlist container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <div class="k_ti">
            投稿一覧
            <img class="cursor tips" v-on:click="this.$root.pushTips('POSTLIST')" :src="this.$root.IMG_PATH + 'btn_hint' + this.$root.theme_img">
          </div>

          <div class="grayBar" />

          <p style="k_mi">シリーズ選択</p>
          <select class="form-select select" :class="this.$root.theme_bc_2" name="series" v-on:change="changeSeries" v-model="selectSeries">
            <option v-for="(seriesName, index) in seriesList" :key="index" v-bind:value="seriesName">{{seriesName}}</option>
          </select>
          <div class="grayBar" />
          <post-list-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></post-list-row>
          <subPagenation
            :showPages="showPages"
            :currentPage="currentPage"
            :totalCount="totalCount"
            :perPage="perPage"
            :totalPages="totalPages"
            @currentPage="getCurrentPage"
          ></subPagenation>
        </div>
        <br>
      </div>
      <ad-area></ad-area>
    </div>
  </div>
</template>

<script>
import SubPagenation from './SubPagenation.vue';
import axios from 'axios'

export default {
  name: 'PostList',
  components: {
    SubPagenation
  },
  data() {
    return {
      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 10, //1ページの表示件数
      totalCount: 15, //取得したアイテムの総件数
      totalPages: 3, //総ページ数

      selectSeries : 0,
      allPostDataList: null,
      filterDataList : null,
      dispDataList: null,
      seriesList: ["すべて","シリーズA", "シリーズB", "シリーズC", "シリーズD"]
    };
  },
  computed : {
  },
  methods : {
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    changeSeries : function(e) {
      let query = Object.assign({}, this.$route.query);
      query["series"] = e.target.value;
      query["page"] = 1;
      this.$router.push({query});
    },
    getMyPostList : function () {
      //パラメータチェック
      let query = Object.assign({}, this.$route.query);
      let page = query["page"];
      let series = query["series"];

      //ページが存在しないまたはマイナス
      if (page == null || page < 0) {
        query["page"] = 1;
        this.$router.replace({query});
        return;
      }
      //シリーズが存在しない場合
      if (series == null) {
        query["series"] = "すべて";
        this.$router.replace({query});
        return;
      }

      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + 'getMyPostList', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        let resData = response.data;
        let resPostList = resData["postList"];
        let baseInfo =resData["baseInfo"];
        this.allPostDataList = [];
        for (let i = 0; i < resPostList.length; i++) {
          let postData = resPostList[i];

          var tagStr = postData["tags"];
          var tagArray = {};
          if (tagStr != "") {
            tagArray = tagStr.split(',');
          }

          var data = {
            postId : postData["post_id"],
            userId : postData["user_id"],
            listType : "listTypeB",
            author : baseInfo["user_name"],
            series : postData["series"],
            title : postData["title"],
            outline : postData["outline"],
            rating : postData["rating"],
            chara : postData["chara"],
            creation : postData["creation"],
            tags : tagArray,
            stamp : postData["stamp_count"],
            bookmark : postData["bookmark_count"],
            view : postData["view_count"],
            length : postData["length"],
            publishing : postData["publishing"],
            postDate : new Date(postData["created_at"])
          };
          this.allPostDataList.push(data);
        }
        
        //シリーズ一覧取得
        this.seriesList = this.$root.retComSeriesList(this.allPostDataList);
        
        //初期処理
        this.initAction();

      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    initAction : function() {
      //パラメータチェック
      let query = Object.assign({}, this.$route.query);
      let page = query["page"];
      let series = query["series"];

      //シリーズチェック
      let seriesFlg = false;
      for (let i = 0; i < this.seriesList.length; i++) {
        if (this.seriesList[i] == series) {
          seriesFlg = true;
        }
      }

      //シリーズが存在しない場合
      if (seriesFlg == false) {
        query["series"] = "すべて";
        this.$router.replace({query});
        return;
      }

      this.currentPage = page;
      this.selectSeries = series;

      //シリーズから絞り込み
      this.filterDataList = this.$root.retComFilterList(this.allPostDataList, series);
      this.$root.log(this.filterDataList);

      //ページ数
      this.totalCount = this.filterDataList.length;
      var tPage = Math.floor(this.totalCount / 10);
      if (this.totalCount % 10 != 0) {
        tPage = tPage + 1;
      }
      this.totalPages = tPage;

      //ページ数が最大を超えている場合
      if (this.totalPages < this.currentPage) {
        query["page"] = this.totalPages;
        this.$router.replace({query});
      }

      //ページを表示
      this.dispDataList = this.$root.retComPagingList(this.currentPage, this.filterDataList);

      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getMyPostList();
      vm.$root.setCurrentPage("POSTLIST");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getMyPostList();
      vm.$root.setCurrentPage("POSTLIST");
    });
  },
  watch : {
    '$route'() {
      if (this.$route.name == "PostList") {
        this.$root.showLoad();
        this.getMyPostList();
      }
    }
  }
}
</script>

