<template>
  <div class="reissuePassword" style="text-align:center; width:270px; margin:auto">
    <div v-if="isSend == false">
      <div style="height:36px" />
      <span class="p12">パスワード再発行</span>
      <div style="height:22px" />
      <span class="p9">アカウントに登録したメールアドレスを入力してください</span>
      <div style="height:14px" />
      <p class="errMsg" v-if="errorMessageData.mailAddress">▼ {{errorMessageData.mailAddress}}</p>
      <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" style="width:100%" v-model="mailAddress" maxlength="200" autocomplete="off" :placeholder="this.$root.PLACEHOLDER_MAIL"><br>
      <div style="height:20px" />
      <p class="errMsg" v-if="errorMessageData.reissue">▼ {{errorMessageData.reissue}}</p>
      <span class="btn" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="pushSend">送信</span>
      <br>
      <br>
      <br>
      <br>
    </div>
    <div v-if="isSend == true">
      <div style="height:36px" />
      <span class="p12">再発行申請完了</span>
      <div style="height:20px" />
      <span class="p9">パスワード再設定用のメールを送信しました。<br>受信したメールに記載されているURLより、<br>パスワード設定ページへアクセスしてください。<br>パスワード再設定用のURLの有効期限は1時間です。</span>
      <p>{{ this.url }}</p>
      <div style="height:38px" />
      <span class="btn" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="pushHome">Topに戻る</span>
      <br>
      <br>
      <br>
      <br>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'ReissuePassword',
  components: {
  },
  data() {
    return {
      isSend : false,
      mailAddress : "",
      url : "",
      
      errorMessageData : {
        mailAddress : "",
        reissue : ""
      }
    }
  },
  computed : {
  },
  methods : {
    pushSend : function() {
      this.$root.showLoad();

      this.errorMessageData.mailAddress = "";
      this.errorMessageData.reissue = "";

      //トリム
      this.id = this.mailAddress.trim();
      
      //入力チェックa
      if (this.id == "") {
        this.errorMessageData.mailAddress = "メールアドレスの文字数は1文字以上入力してください";
      } else if (this.id.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.mailAddress = "メールアドレスの文字数は200文字以下で入力してください";
      } else {
        let val = this.$root.checkAddress(this.id);
        if (val != 0) {
          this.errorMessageData.mailAddress = "メールアドレスに誤りがあります。もう一度入力してください";
        }
      }

      if (this.errorMessageData.mailAddress != "") {
        this.$root.closePopup();
        return;
      }
      
      //再発行申請
      let params = new URLSearchParams();
      params.append('mailAddress', this.mailAddress);
      axios.post(this.$root.SERVER_PATH + 'requestReset', params).then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          //パスワードリセット（存在なし）
          this.errorMessageData.reissue = "該当のアカウントは存在しません";
          this.$root.closePopup();
          return;
        }

        if (!this.$root.checkServerResponse(response, false)) {
          return;
        }

        this.url = "http://localhost:8001/" + "resetPassword/" + resData["tempId"];
        this.$root.log("http://localhost:8001/resetPassword/" + resData["tempId"]);
        this.isSend = true;
        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    pushHome : function () {
      this.$router.push('/');
    },
    initAction : function() {
      axios.get(this.$root.SERVER_PATH + 'isAuth')
      .then((response) => {
        this.$root.log(response.data);

        if (!this.$root.checkServerResponse(response, false)) {
          return;
        }

        if (this.$root.isLogin) {
          //ログイン済みの場合はメインメニューへ
          this.$router.push("/mainmenu");
        } else {
          this.$root.closePopup();
        }
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.twitterId = "";
      vm.twitterName = "";
      vm.twitterProfile = "";
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.twitterId = "";
      vm.twitterName = "";
      vm.twitterProfile = "";
      vm.initAction();
    });
  }
}
</script>

