<template>
  <div class="userpage container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <div style="display:flex; align-items: center; height:70px">
            <img :src="$root.ICON_PATH + this.upUserId + '.png'" style="width:70px; height:70px; border-radius:50%">
            <span class="p14" style="margin-left:20px">{{upUserName}}</span>
          </div>
          
          <div class="sp_line_s" />
          
          <p class="p14">{{upUserProfile}}</p>
          <div v-show="this.upUserTwitterCode != ''">
            <div class="sp_line_s" />
            <p v-on:click="pushTwitterLink" class="link" :class="this.$root.theme_fc_1">@{{this.upUserTwitterCode}}</p>
          </div> 
          
          <div v-show="this.upLinkList.length != 0">
            <div class="sp_line_m" />

            リンク:
            <div class="sp_line_s" />
            <a v-for="(links, index) in this.upLinkList" :key="index" class="link"
             :class="this.$root.theme_fc_1" target="_blank" style="margin-left:10px" v-bind:href="links[1]">{{ links[0] }}</a>
          </div>
          

          <div class="grayBar" />

          <span class="btn btn-on btn-icon btn-icon-follow-on" :class="[this.$root.theme_boc_1, this.$root.theme_fc_1, theme_btn_icon_follow_on]" v-show="upUserFollow == 0" style="width:144px;" v-on:click="onChangeFollow('1')">推し作家にする</span>
          <span class="btn btn-icon btn-icon-follow-off" :class="this.$root.theme_bc_1" v-show="upUserFollow == 1" style="width:144px;" v-on:click="onChangeFollow('0')">推し作家を解除</span>

          <div class="sp_line_s" />

          <span class="btn btn-on btn-icon btn-icon-mute-on" :class="[this.$root.theme_boc_1, this.$root.theme_fc_1, theme_btn_icon_mute_on]" v-show="upUserMute == 0" style="width:144px;" v-on:click="onChangeMute('1')">ユーザーミュート</span>
          <span class="btn btn-icon btn-icon-mute-off" :class="this.$root.theme_bc_1" v-show="upUserMute == 1" style="width:144px;" v-on:click="onChangeMute('0')">ミュート解除</span>
        </div>
  
        <div class="sp_line_m" />
  
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <select class="form-select" :class="this.$root.theme_bc_2" name="series" v-on:change="changeSeries" v-model="selectSeries">
            <option v-for="(seriesName, index) in seriesList" :key="index" v-bind:value="seriesName">{{seriesName}}</option>
          </select>
          <div class="grayBar" />
          <post-list-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></post-list-row>
          <subPagenation
            :showPages="showPages"
            :currentPage="currentPage"
            :totalCount="totalCount"
            :perPage="perPage"
            :totalPages="totalPages"
            @currentPage="getCurrentPage"
          ></subPagenation>
        </div>
      </div>
      <br>
    </div>
    <popup-base ref="popup" @popupAction="popupAction" :kbn="this.popupKbn" :params="this.popupParams"></popup-base>
  </div>
</template>

<script>
import SubPagenation from './SubPagenation.vue';
import axios from 'axios'

export default {
  name: 'UserPage',
  components: {
    SubPagenation
  },
  data() {
    return { 
      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 10, //1ページの表示件数
      totalCount: 15, //取得したアイテムの総件数
      totalPages: 3, //総ページ数

      selectSeries : "",
      allPostDataList: [],
      filterDataList : [],
      dispDataList: [],
      seriesList: ["シリーズA", "シリーズB", "シリーズC", "シリーズD"],

      upUserId : "",
      upUserName : "",
      upUserProfile : "",
      upUserTwitterCode : "",
      upUserFollow : 0,
      upUserMute : 0,
      upLinkList : [],

      popupKbn : "",
      popupParams : {
        title : "",
        body : "",
        tweet : "",
      },
    };
  },
  computed : {
    theme_btn_icon_follow_on : function() {
      if (this.$root.themeIndex == 0) {
          return "btn-icon-follow-on_1";
        } else if (this.$root.themeIndex == 1) {
          return "btn-icon-follow-on_2";
        } else if (this.$root.themeIndex == 2) {
          return "btn-icon-follow-on_3";
        } else if (this.$root.themeIndex == 3) {
          return "btn-icon-follow-on_4";
        }
        return "btn-icon-follow-on_1";
    },
    theme_btn_icon_mute_on : function() {
      if (this.$root.themeIndex == 0) {
          return "btn-icon-mute-on_1";
        } else if (this.$root.themeIndex == 1) {
          return "btn-icon-mute-on_2";
        } else if (this.$root.themeIndex == 2) {
          return "btn-icon-mute-on_3";
        } else if (this.$root.themeIndex == 3) {
          return "btn-icon-mute-on_4";
        }
        return "btn-icon-mute-on_1";
    },
  },
  methods : {
    pushTwitterLink : function () {
      window.open("https://twitter.com/" + this.upUserTwitterCode, '_blank');
    },
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    changeSeries : function(e) {
      let query = Object.assign({}, this.$route.query);
      query["series"] = e.target.value;
      query["page"] = 1;
      this.$router.push({query});
    },
    onChangeFollow : function(val) {
      this.$root.showLoad();

      let params = new URLSearchParams();
      params.append('followId', this.$route.params.userId);
      params.append('value', val);
      
      axios.post(this.$root.SERVER_PATH + 'updateFollow', params)
      .then((response) => {
        // let resData = response.data;
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        this.upUserFollow = val;
        this.popupKbn = "UPDATE";
        this.popupParams = {
          title : "推し作家",
          body : "変更しました",
        }

        this.$root.closePopup();
        this.$refs.popup.showModal();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    onChangeMute : function(val) {
      this.$root.showLoad();

      let params = new URLSearchParams();
      params.append('muteId', this.$route.params.userId);
      params.append('value', val);

      axios.post(this.$root.SERVER_PATH + 'updateMute', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        this.upUserMute = val;
        this.popupKbn = "UPDATE";
        this.popupParams = {
          title : "ミュート",
          body : "変更しました",
        }

        this.$root.closePopup();
        this.$refs.popup.showModal();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    getUserInfo : function () {
      this.upUserId = this.$route.params.userId;
      let params = new URLSearchParams();
      params.append('targetId', this.upUserId);

      axios.post(this.$root.SERVER_PATH + 'getAuthorInfo', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          //データなし
          this.$router.replace({path : "/error", query : {errorCode : resData["status"], message : "該当のユーザーは存在しません"}});
          return;
        }
        
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        
        let postList = resData["postList"];
        this.upUserFollow = resData["follow"];
        this.upUserMute = resData["mute"];

        this.allPostDataList = [];
        
        this.upUserName = resData["user_name"];
        this.upUserProfile = resData["user_profile"];
        this.upUserTwitterCode = resData["twitter_code"];

        for (let i = 0; i < postList.length; i++) {
          let postData = postList[i];

          var tagStr = postData["tags"];
          var tagArray = {};
          if (tagStr != "") {
            tagArray = tagStr.split(',');
          }

          var data = {
            postId : postData["post_id"],
            userId : this.upUserId,
            listType : "listTypeC",
            author : resData["user_name"],
            series : postData["series"],
            title : postData["title"],
            outline : postData["outline"],
            rating : postData["rating"],
            chara : postData["chara"],
            creation : postData["creation"],
            tags : tagArray,
            length : postData["length"],
            publishing : postData["publishing"],
            postDate : new Date(postData["created_at"])
          };

          this.allPostDataList.push(data);
        }

        //リンク
        this.upLinkList = [];
        let linkList = resData["linkList"];
        console.log(linkList);
        for (let i = 0; i < linkList.length; i++) {
          this.upLinkList.push([linkList[i][0], linkList[i][1]]);
        }
        console.log(this.upLinkList);
        //シリーズ一覧取得
        this.seriesList = this.$root.retComSeriesList(this.allPostDataList);

        //初期処理
        this.initAction();

      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    initAction : function() {
      //パラメータチェック
      let query = Object.assign({}, this.$route.query);
      let page = query["page"];
      let series = query["series"];

      //ページが存在しないまたはマイナス
      if (page == null || page < 0) {
        query["page"] = 1;
        this.$router.replace({query});
        return;
      }
      //シリーズが存在しない場合
      if (series == null) {
        query["series"] = "すべて";
        this.$router.replace({query});
        return;
      }
      console.log(series + "AAA");
      //シリーズチェック
      let seriesFlg = false;
      for (let i = 0; i < this.seriesList.length; i++) {
        if (this.seriesList[i] == series) {
          seriesFlg = true;
        }
      }
      //シリーズが存在しない場合
      if (seriesFlg == false) {
        query["series"] = "すべて";
        this.$router.replace({query});
        return;
      }

      this.currentPage = page;
      this.selectSeries = series;

      //シリーズから絞り込み
      this.filterDataList = this.$root.retComFilterList(this.allPostDataList, series);
      this.$root.log(this.filterDataList);

      //ページ数
      this.totalCount = this.filterDataList.length;
      var tPage = Math.floor(this.totalCount / 10);
      if (this.totalCount % 10 != 0) {
        tPage = tPage + 1;
      }
      this.totalPages = tPage;

      //ページ数が最大を超えている場合
      if (this.totalPages < this.currentPage) {
        query["page"] = this.totalPages;
        this.$router.replace({query});
      }

      //ページを表示
      this.dispDataList = this.$root.retComPagingList(this.currentPage, this.filterDataList);

      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getUserInfo();
      vm.$root.setCurrentPage("USERPAGE");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.getUserInfo();
      vm.$root.setCurrentPage("USERPAGE");
    });
  },
  watch : {
    '$route'() {
      if (this.$route.name == "UserPage") {
        let curUserId = this.$route.params.userId;
        if (this.upUserId != curUserId) {
          //ユーザーが違う場合
          this.$root.showLoad();
          this.getUserInfo();
        } else {
          //ページ遷移
          this.$root.showLoad();
          this.initAction();
        }
      }
    }
  }
}
</script>

