<template>
  <div class="reading container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <post-list-row v-bind:rowData='rowData'></post-list-row>
          <div v-if="reConversionList != null" class="container-fluid">
            <div class="row k_mi">変換項目<img class="cursor tips" v-on:click="this.$root.pushTips('READING_CONVERSION')" :src="this.$root.IMG_PATH + 'btn_hint' + this.$root.theme_img"></div>

            <div v-for="(conversion, index) in reConversionList" :key="index">
              <span class="p12">{{reConversionList[index][0]}}</span>
              <div class="sp_line_ss" />
              <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]"
               v-model="reConversionList[index][2]" style="width:100%"  :placeholder="conversionPlaceholder(reConversionList[index][1])" maxlength="10">

              <div v-show="index != this.reConversionList.length - 1" class="sp_line_s" />
            </div>

            <div class="sp_line_m" />

            <p class="errMsg" v-if="errorMessageData.conversion"><br>{{errorMessageData.conversion}}</p>
            <span class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushConversion">名前変換</span>

            <div class="grayBar" />
          </div>

          <!-- 本文の表示サイズ -->
          <div style="text-align:right">
            <img :src="this.$root.IMG_PATH + 'btn_text_fontsize.png'" class="cursor" style="width:20px; height:20px" v-on:click="pushBodyFontSize">
            <span class="p12">: {{ bodyFontSizeStr }}</span>
            <img :src="this.$root.IMG_PATH + 'btn_text_kerning.png'" class="cursor" style="width:20px; height:20px; margin-left:20px" v-on:click="pushBodyKerning">
            <span class="p12">: {{ bodyKerningStr }}</span>
          </div>

          <div class="sp_line_s" />

          <div v-show="publishingStatus == 0 && filterStatus == 0">
            <span oncontextmenu="return false;" oncopy="return false;" :class="bodyAppearance">{{body}}</span>
          </div>

          <div v-show="filterStatus != 0 || publishingStatus != 0" style="padding:20px 20px; text-align:center">
            <img v-show="filterStatus != 0" :src="filterImgPath" style="width:104px; height:129px">
            <img v-show="publishingStatus != 0" :src="publishingImgPath" style="width:200px;">
            <div style="height:12px" />
            <div v-show="publishingStatus == 1">
              <!-- パスワード -->
              <p>{{publishingPasswordHint}}</p>
              <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" maxlength="20" v-model="publishingPassword" style="width:100%" placeholder="パスワードを入力してください">
            </div>
            <span v-show="isDispBtn" class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushAgreement">全て表示</span>
          </div>
        </div>

        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px; margin-top:18px">
          <p class="p12">応援スタンプ<img class="cursor tips" v-on:click="this.$root.pushTips('READING_STAMP')" :src="this.$root.IMG_PATH + 'btn_hint' + this.$root.theme_img"></p>
          <div v-show="this.stampList.length != 0">
            <span v-for="(stName, index) in stampList" :key="index" class="stamp" :class="stName" />
          </div>
          <div v-show="this.stampList.length == 0" style="height:30px" />
          <div class="grayBar" />
          <span class="p12">リアクションをたくさん送って愛を伝えよう！</span>
          <div class="sp_line_s" />
          <div>
            <!-- <a v-for="(stName, index) in defStampList" :key="index" href="#" v-on:click.prevent.stop="pushStamp(stName)" >
              <img :src="this.$root.IMG_PATH + 'stamp/' + stName + '.png'" class="stamp" :style="stampPosi(index)">
            </a> -->
              <span v-for="(stName, index) in defStampList" :key="index" class="stamp" :class="stName" v-on:click.prevent.stop="pushStamp(stName)"></span>
          </div>
        </div>
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px; margin-top:18px">
          <post-list-row v-bind:rowData='rowData'></post-list-row>
          <span class="btn btn-on btn-icon btn-icon-bookmark-on" :class="[this.$root.theme_boc_1, this.$root.theme_fc_1, theme_btn_icon_bookmark_on]" style="width:145px" v-show="isBookmark == 0" v-on:click="pushBookmark(1)">本棚に追加</span>
          <span class="btn btn-icon btn-icon-bookmark-off" :class="this.$root.theme_bc_1" style="width:145px" v-show="isBookmark == 1" v-on:click="pushBookmark(0)">本棚から返却</span>
          <div class="sp_line_s" />
          <span class="btn btn-on btn-icon btn-icon-bookmark-on" :class="[this.$root.theme_boc_1, this.$root.theme_fc_1, theme_btn_icon_bookmark_on]" style="width:145px" v-on:click="pushShare">共有する</span>
        </div>
      </div>
      <br>
      <ad-area></ad-area>
    </div>
  </div>
  <popup-base ref="popup" @popupAction="popupAction" :kbn="this.popupKbn" :params="this.popupParams"></popup-base>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Reading',
  components: {
  },
  data() {
    return {
      reConversionList : null,
      title : "",
      preBody : "",
      body : "",
      rowData : null,
      isBookmark : 0,
      stampList : [],
      filterStatus : 0,
      publishingStatus : 0,
      publishingPassword : "",
      publishingPasswordHint : "",

      defStampList : [
      ],

      errorMessageData : {
        conversion : "",
      },

      popupKbn : "",
      popupParams : {
        title : "",
        body : "",
        tweet : "",
      },

      isLimit : false,
      isDispBtn : true,

      bodyFontSize : 0,
      bodyKerning : 0,
    };
  },
  computed : {
    conversionPlaceholder : function() {
      return (placeholder)=> {
        return  placeholder.replace(/#/g, '');
      }
    },
    bodyAppearance : function() {
      let fontSize = "p12";
      let kerning = "kerningLow";
      if (this.bodyFontSize == 1) {
        fontSize = "p14";
      } else if (this.bodyFontSize == 2) {
        fontSize = "p16";
      }

      if (this.bodyKerning == 1) {
        kerning = "kerningHigh";
      }
      return fontSize + " " + kerning;
    },
    bodyFontSizeStr : function () {
      if (this.bodyFontSize == 0) {
        return "小";
      } else if (this.bodyFontSize == 1) {
        return "中";
      } else if (this.bodyFontSize == 2) {
        return "大";
      }
      return "";
    },
    bodyKerningStr : function () {
      if (this.bodyKerning == 0) {
        return "狭い";
      } else if (this.bodyKerning == 1) {
        return "広い";
      }
      return "";
    },
    retStampPosi : function () {
      return (stampId)=> {
        let stNo = parseInt(stampId.replace('st', ''), 10);
        let x = stNo % 10 * -1;
        let y = Math.floor(stNo / 10) * -1;
        return  "background-position:" + (x * 30) + "px " + (y * 30) + "px";
      }
    },
    stampPosi : function () {
      return (val)=>{
        let x = val % 10 * -1;
        let y = Math.floor(val / 10) * -1;
        return  "background-position:" + (x * 30) + "px " + (y * 30) + "px";
      }
    },
    filterImgPath : function () {
      this.$root.log(this.filterStatus);
      if (this.filterStatus == 1) {
        return this.$root.IMG_PATH + "fil_cushion" + this.$root.theme_img;
      } else if (this.filterStatus == 2) {
        return this.$root.IMG_PATH + "fil_netabare" + this.$root.theme_img;
      } else if (this.filterStatus == 3) {
        return this.$root.IMG_PATH + "fil_r18" + this.$root.theme_img;
      } else if (this.filterStatus == 4) {
        return this.$root.IMG_PATH + "fil_r18g" + this.$root.theme_img;
      } else if (this.filterStatus == 5) {
        return this.$root.IMG_PATH + "fil_pg12" + this.$root.theme_img;
      }
      return "";
    },
    publishingImgPath : function () {
      this.$root.log(this.publishingStatus);
      if (this.publishingStatus == 1) {
        return this.$root.IMG_PATH + "pub_password" + this.$root.theme_img;
      } else if (this.publishingStatus == 2) {
        return this.$root.IMG_PATH + "pub_sougo" + this.$root.theme_img;
      } else if (this.publishingStatus == 3) {
        return this.$root.IMG_PATH + "pub_follower" + this.$root.theme_img;
      } else if (this.publishingStatus == 4) {
        return this.$root.IMG_PATH + "pub_list" + this.$root.theme_img;
      }
      return "";
    },
    theme_btn_icon_bookmark_on : function() {
      if (this.$root.themeIndex == 0) {
          return "btn-icon-bookmark-on_1";
        } else if (this.$root.themeIndex == 1) {
          return "btn-icon-bookmark-on_2";
        } else if (this.$root.themeIndex == 2) {
          return "btn-icon-bookmark-on_3";
        }
        return "btn-icon-bookmark-on_1";
    },
  },
  methods : {
    pushBodyFontSize : function() {
      console.log("IN");
      if (this.bodyFontSize == 0) {
        this.bodyFontSize = 1;
      } else if (this.bodyFontSize == 1) {
        this.bodyFontSize = 2;
      } else if (this.bodyFontSize == 2) {
        this.bodyFontSize = 0;
      }
      localStorage.setItem("BODY_FONTSIZE_" + this.$root.userData.userId, this.bodyFontSize);
    },
    pushBodyKerning : function() {
      if (this.bodyKerning == 0) {
        this.bodyKerning = 1;
      } else if (this.bodyKerning == 1) {
        this.bodyKerning = 0;
      }
      localStorage.setItem("BODY_KERNING_" + this.$root.userData.userId, this.bodyKerning);
    },
    pushShare : function() {
      var url = "https://twitter.com/intent/tweet?" + 
        "hashtags=" + encodeURIComponent('yumedrop,夢小説,テスト') + 
        "&text=" + encodeURIComponent("ユメドロップ作品ページだよ") + 
        "&url=" + encodeURIComponent(this.$root.BASE_URL + "reading/" + this.rowData.postId) + 
        "&via=" + encodeURIComponent("iphonetaro_live");
        window.open(url);
    },
    pushBookmark : function(val) {
      this.$root.showLoad();

      let params = new URLSearchParams();
      params.append('postId', this.$route.params.postId);
      params.append('value', val);
      axios.post(this.$root.SERVER_PATH + 'updateBookmark', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        this.isBookmark = val;
        this.$root.closePopup();
      }).catch((error) => {          
        this.$root.comNetworkError(error);
      });
    },
    pushAgreement : function() {
      if (this.publishingStatus == 0) {
        //フィルターだけの場合
        this.filterStatus = 0;
        return;
      } else {
        if (this.$root.userData.isAuthTwitter == false) {
          //twitter連携をしていない場合
          this.popupKbn = "READING_PUBLISH";
          this.popupParams = {
            title : "Twitter連携",
            body : "この作品はTwitter連携が必要です\n設定からTwitter連携を行ってください",
          }
          this.$refs.popup.showModal();
          return;
        }

        this.$root.showLoad();

        let params = new URLSearchParams();
        params.append('postId', this.$route.params.postId);

        //公開方法別チェック
        if (this.publishingStatus == 1) {
          //パスワード
          params.append('kbn', "PASSWORD");
          params.append('password', this.publishingPassword);
        } else if (this.publishingStatus == 2) {
          //相互フォロー
          params.append('kbn', "TWITTER_SOUGO");
          params.append('userId', this.rowData.userId);
       } else if (this.publishingStatus == 3) {
          //フォロー
          params.append('kbn', "TWITTER_FOLLOW");
          params.append('userId', this.rowData.userId);
        } else if (this.publishingStatus == 4) {
          //リスト
          params.append('kbn', "TWITTER_LIST");
        }

        axios.post(this.$root.SERVER_PATH + 'checkPublishing', params)
        .then((response) => {
          if (!this.$root.checkServerResponse(response, true)) {
            return;
          }
          let resData = response.data;
          if (resData["status"] == this.$root.API_SUCCESS) {
            //OK
            let body = resData["body"];
            this.body = body;            
            this.preBody = this.body;
            this.publishingStatus = 0;
            this.filterStatus = 0;
          } else {
            //NG
            let errBody = "";
            let errTitle = "Twitter連携エラー";
            let errorNo = resData["error"];
            if (errorNo == "L1") {
              //メンバーじゃない
              errBody = "指定された公開リストのメンバーではありません";
            } else if (errorNo == "L2") {
              //リスト存在なし
              errBody = "著者が指定したリストが存在しません";
            } else if (errorNo == "P1") {
              //パスワード不一致
              errTitle = "パスワードエラー";
              errBody = "パスワードが間違っています";
            } else if (errorNo == "S1") {
              //相互不一致
              errBody = "著者のTwitterアカウントと相互フォローではありません";
            } else if (errorNo == "F1") {
              //フォロー不一致
              errBody = "著者のTwitterアカウントをフォローしていません";
            } else if (errorNo == "99") {
              //api取得上限
              this.popupKbn = "READING_PUBLISH";
              this.popupParams = {
              title : this.$root.STR_TITLE_TWITTER_API_LIMIT,
              body : this.$root.STR_BODY_TWITTER_API_LIMIT,
              }
              this.$root.closePopup();
              this.$refs.popup.showModal();
              this.isDispBtn = false;
              return;
            }
            this.popupKbn = "READING_PUBLISH";
            this.popupParams = {
              title : errTitle,
              body : errBody,
            }
            this.$root.closePopup();
            this.$refs.popup.showModal();

            if (this.publishingStatus == 2 || this.publishingStatus == 3 || this.publishingStatus == 4) {
              this.isDispBtn = false;
            }
            return;
          }
          this.$root.closePopup();
        }).catch((error) => {          
        this.$root.comNetworkError(error);
      });
      }

    },
    getReadingData : function() {
      for (let i = 1; i < 88; i++) {
        this.defStampList.push("st" + ('000' + i).slice(-3));
      }

      let params = new URLSearchParams();
      params.append('postId', this.$route.params.postId);
      axios.post(this.$root.SERVER_PATH + 'getReadingData', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          //該当データなし
          this.$router.replace({path : "/error", query : {errorCode : resData["status"], message : "該当の投稿は存在しません"}});
          return;
        }
        
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        let postData = resData["postData"];
        var tagStr = postData["tags"];
        var tagArray = {};
        if (tagStr != "") {
          tagArray = tagStr.split(',');
        }
        this.$root.log("IN");
        this.rowData = {
          postId : postData["post_id"],
          userId : postData["user_id"],
          listType : "listTypeD",
          author : postData["user_name"],
          series : postData["series"],
          title : postData["title"],
          outline : postData["outline"],
          rating : postData["rating"],
          chara : postData["chara"],
          creation : postData["creation"],
          tags : tagArray,
          length : postData["length"],
          postDate : new Date(postData["created_at"]),
        };

        this.isBookmark = parseInt(postData.bookmark);
        this.title = postData["title"];

        
        this.stampList = [];
        let stList = resData["stamp"];
        for (let i = 0; i < stList.length; i++) {
          this.stampList.push(stList[i]["stamp_id"]);
        }

        //フィルター
        this.filterStatus = postData["filter"];

        //公開方法
        if (this.$root.userData.userId == postData["user_id"]) {
          this.pusblishing = 0;
        } else {
          this.publishingStatus = postData["publishing"];
          this.$root.log(this.filterStatus + "  " + this.publishingStatus);
          if (this.publishingStatus == 1) {
            this.publishingPasswordHint = "ヒント：" + postData["publishing_sub2"];
          }
        }

        this.body = postData["body"];
        this.preBody = this.body;


        //変換項目
        this.reConversionList = null;
        var conversionStr = postData["conversion"];
        if (conversionStr != "") {
          this.reConversionList = [];
          var cArray1 = conversionStr.split(this.$root.CONVERSION2);
          for (let i = 0; i < cArray1.length; i++) {
            var cArray2 = cArray1[i].split(this.$root.CONVERSION1);
            this.reConversionList.push([cArray2[0], cArray2[1], ""]);       
          }
        }

        //本文の体裁
        this.bodyFontSize = localStorage.getItem("BODY_FONTSIZE_" + this.$root.userData.userId) || 0;
        this.bodyKerning = localStorage.getItem("BODY_KERNING_" + this.$root.userData.userId) || 0;

        this.$root.closePopup();
      }).catch((error) => {          
        this.$root.comNetworkError(error);
      });
    },
    pushConversion : function() {
      this.errorMessageData.conversion = "";

      for (let i = 0; i < this.reConversionList.length; i++) {
        if (this.reConversionList[i][2] == "") {
          this.errorMessageData.conversion = "変換文字は1文字以上入力してください";
          return;
        } else if (this.reConversionList[i][2].length > 10) {
          this.errorMessageData.conversion = "変換文字は10文字以下で入力してください";
          return;
        }  else {
          let regStr = this.$root.checkRegulation(this.reConversionList[i][2])  
          if (regStr != "") {
            this.edErrorMessageData.conversion = this.$root.STR_ERROR_REGULATION + regStr;
            return;
          }
        }
      }

      var body = this.preBody;
      for (let i = 0; i < this.reConversionList.length; i++) {
        body = body.replace(this.reConversionList[i][1], this.reConversionList[i][2]);
      }
      this.body = body;

      this.popupKbn = "COMMON";
      this.popupParams = {
        title : "",
        body : "変換しました",
      }
      this.$refs.popup.showModal();
      return;

    },
    pushStamp : function (stName) {
      if (this.isLimit) {
          this.popupKbn = "STAMP";
          this.popupParams = {
            title : "",
            body : "1作品に押せるスタンプは10個までです",
          }
          this.$refs.popup.showModal();
          this.isLimit = true;
          return;
      }

      this.$root.showLoad();

      let params = new URLSearchParams();
      params.append('postId', this.$route.params.postId);
      params.append('userId', this.$root.userData.userId);
      params.append('stamp', stName);
      axios.post(this.$root.SERVER_PATH + 'addStamp', params)
      .then((response) => {
        if (response.data["status"] == this.$root.API_FAILED_LIMIT) {
          this.popupKbn = "STAMP";
          this.popupParams = {
            title : "スタンプ上限",
            body : "1作品に押せるスタンプは10個までです",
          }
          this.$root.closePopup();
          this.$refs.popup.showModal();
          this.isLimit = true;
          return;
        } else if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        this.stampList.push(stName);
        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.log("INNNN");
      vm.$root.showLoad();
      vm.getReadingData();
      vm.$root.setCurrentPage("READING");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.log("INNNN2");
      vm.$root.showLoad();
      vm.getReadingData();
      vm.$root.setCurrentPage("READING");
    });
  }
}
</script>

<style>
</style>