<template>
  <div class="Contact container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <div v-show="status == 0">
            お問い合わせ
            <div class="grayBar" />
            お名前 <span class="constrains" :class="this.$root.theme_fc_1">※必須</span>
            <p class="errMsg" v-if="errorMessageData.name">▼ {{errorMessageData.name}}</p>
            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="name" style="width:100%" maxlength="50" placeholder="お名前を入力してください">

            <div class="sp_line_l" />

            メールアドレス <span class="constrains" :class="this.$root.theme_fc_1">※必須</span>
            <p class="errMsg" v-if="errorMessageData.mailAddress">▼ {{errorMessageData.mailAddress}}</p>
            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="mailAddress" style="width:100%" maxlength="50" placeholder="メールアドレスを入力してください">

            <div class="sp_line_l" />

            電話番号
            <p class="errMsg" v-if="errorMessageData.phoneNumber">▼ {{errorMessageData.phoneNumber}}</p>
            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="phoneNumber" style="width:100%" maxlength="50" placeholder="電話番号を入力してください">

            <div class="sp_line_l" />

            お問い合わせ内容 <span class="constrains" :class="this.$root.theme_fc_1">※必須</span>
            <p class="errMsg" v-if="errorMessageData.messageBody">▼ {{errorMessageData.messageBody}}</p>
            <textarea id="story" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="messageBody" name="story" rows="10" style="height:auto; width:100%" maxlength="150"
             placeholder="お問い合わせ内容を入力してください"></textarea>

            <div class="sp_line_xl" />
            <span class="btn" :class="this.$root.theme_bc_1" style="width:160px" v-on:click="pushConfirm">送信</span>
          </div>
          <div v-show="status == 1">
            お問い合わせ内容確認

            <div class="grayBar" />
            お名前
            <div class="sp_line_s" />
            <div style="width:90%; margin:auto">
              {{name}}
            </div>
            <div class="sp_line_l" />

            メールアドレス
            <div class="sp_line_s" />
            <div style="width:90%; margin:auto">
              {{mailAddress}}
            </div>
            <div class="sp_line_l" />

            電話番号
            <div class="sp_line_s" />
            <div style="width:90%; margin:auto">
              {{phoneNumber}}
            </div>
            <div class="sp_line_l" />

            お問い合わせ内容
            <div class="sp_line_s" />
            <div style="width:90%; margin:auto">
              {{messageBody}}
            </div>
            
            <div class="sp_line_xl" />
            上記の内容でよろしいでしょうか？
            <div class="sp_line_m" />

            <span class="btn" :class="this.$root.theme_bc_1" style="width:160px" v-on:click="pushSubmit">送信</span>
            <div class="sp_line_s" />
            <span class="btn btn-gray" style="width:160px" v-on:click="pushEdit">修正する</span>

          </div>
          <div v-show="status == 2">
            お問い合わせ
            <div class="grayBar" />
            お問い合わせ頂き誠にありがとうございました。<br>
            お問い合わせ内容を確認させていただき、後ほど担当者よりご回答をさせていただきます。<br>
            恐れ入りますが、今しばらくお待ちいただけますよう、宜しくお願い申し上げます。

            <div style="height:25px" />
            <span class="btn" :class="this.$root.theme_bc_1" style="width:160px" v-on:click="pushTop">TOPに戻る</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Contact',
  components: {
  },
  data() {
    return {
      status : 0,

      name : "",
      mailAddress : "",
      phoneNumber : "",
      messageBody : "",

      errorMessageData : {
        name : "",
        mailAddress : "",
        phoneNumber : "",
        messageBody : "",
      }
    };
  },
  computed : {
  },
  methods : {   
    pushConfirm : function() {
      this.errorMessageData.name = "";
      this.errorMessageData.mailAddress = "";
      this.errorMessageData.phoneNumber = "";
      this.errorMessageData.messageBody = "";

      //トリム
      this.name = this.name.trim();
      this.mailAddress = this.mailAddress.trim();
      this.phoneNumber = this.phoneNumber.trim();
      
      //入力チェック
      if (this.name == "") {
        this.errorMessageData.name = "お名前が未入力です";
      } else if (this.name.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.name = "お名前は50文字以内で入力してください";
      }
      
      if (this.mailAddress == "") {
        this.errorMessageData.mailAddress = "メールアドレスが未入力です";
      } else if (this.mailAddress.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.mailAddress = "メールアドレスは50文字以内で入力してください";
      }
      
      if (this.phoneNumber.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.mailAddress = "電話番号は50文字以内で入力してください";
      }

      if (this.messageBody == "") {
        this.errorMessageData.messageBody = "お問い合わせ内容が未入力です";
      } else if (this.messageBody.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.messageBody = "お問い合わせ内容は50文字以内で入力してください";
      }

      if (this.errorMessageData.name != "" || this.errorMessageData.mailAddress != "" || this.errorMessageData.phoneNumber != "" || this.errorMessageData.messageBody != "") {
        return;
      }

      //確認へ
      this.status = 1;
    },
    pushSubmit : function() {
      this.$root.showLoad();

      let params = new URLSearchParams();
      params.append('name', this.name);
      params.append('mailAddress', this.mailAddress);
      params.append('phoneNumber', this.phoneNumber);
      params.append('body', this.messageBody);
      
      axios.post(this.$root.SERVER_PATH + 'contact', params)
      .then((response) => {
        this.$root.log(response.data);
        if (!this.$root.checkServerResponse(response, false)) {
          return;
        }

        this.$root.closePopup();
        this.status = 2;
      });
    },
    pushEdit : function() {
      this.status = 0;
    },
    pushTop : function() {
      this.$router.push("/");
    },
    initAction : function() {
      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.$root.getBaseInfo();
      vm.$root.setCurrentPage("");
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.$root.getBaseInfo();
      vm.$root.setCurrentPage("");
      vm.initAction();
    });
  }
}
</script>

