            <input type="text" class="input-text searchBox-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="seaKeyword" style="width:100%" maxlength="50" placeholder="検索ワードを入力してください">
