              </div>
              <div>
                <span :class="this.btnClass(this.opLength, 1)" v-on:click="pushSeOpLength(1)" style="width:120px">2000文字以上</span>
              </div>
              <div>
                <span :class="this.btnClass(this.opLength, 2)" v-on:click="pushSeOpLength(2)" style="width:120px">5000文字以上</span>
              </div>
              <div>
                <span :class="this.btnClass(this.opLength, 3)" v-on:click="pushSeOpLength(3)" style="width:120px">10000文字以上</span>
