                <div class="col-4" id="CHK" :class="this.$root.theme_chk_1" v-show="this.$root.userData.isDispR18 == true">
                  <div style="display:flex">
                    <label class="typeA-checkbox">
                      <input type="checkbox"  v-model="opRating[1]" true-value="1" false-value="0">
