              <div class="sp_line_m" />

              <p class="k_mi">名前変換</p>
              <div class="row" style="line-height:2.5">
                <div class="col-4" id="CHK" :class="this.$root.theme_chk_1">
                  <label class="typeA-checkbox">
                    <input type="checkbox"  v-model="opConversion[0]" true-value="1" false-value="0">
                    <div class="indicator"></div>
                  </label>
                  <span>あり</span>
                </div>
                <div class="col-4" id="CHK" :class="this.$root.theme_chk_1">
                  <label class="typeA-checkbox">
                    <input type="checkbox"  v-model="opConversion[1]" true-value="1" false-value="0">
                    <div class="indicator"></div>
                  </label>
                  <span>なし</span>
                </div>
              </div>
