        <div v-show="this.favoriteTagList.length != 0" style="border-radius:6px; background-color:#ffffff; padding:14px 12px; margin-top:18px">
          <div>
            <img :src="this.$root.IMG_PATH + 'icon_tag.png'" style="width:15px; margin-right:10px"><span>お気に入りタグ</span><br>
            <div class="sp_line_s" />
            <span  v-for="(tag, index) in favoriteTagList" :key="index">
              <span class="cursor " :class="this.$root.theme_fc_1" v-on:click.prevent.stop="pushTag(tag)">#{{tag}}</span>&nbsp;
            </span>
