        </div>
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px; margin-top:18px">
          <div class="container">
            <post-list-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></post-list-row>
          </div>
          <subPagenation
            :showPages="showPages"
            :currentPage="currentPage"
            :totalCount="totalCount"
            :perPage="perPage"
            :totalPages="totalPages"
            @currentPage="getCurrentPage"
            ref="pagenation"
          ></subPagenation>
