<script>
import axios from 'axios';
import SubPagenation from './SubPagenation.vue';
export default {
  name: 'Search',
  components: {
    SubPagenation
  },
  data() {
    return {
      seaKeyword : "",

      isFilterOpen :false,

      filBtnOn : "btn btn-sq " + this.$root.theme_bc_1,
      filBtnOff : "btn btn-sq btn-sq-gray",

      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 10, //1ページの表示件数
      totalCount: 15, //取得したアイテムの総件数
      totalPages: 3, //総ページ数

      opOrder : -1,
      opDuration : 0,
      opLength : 0,
      opRating : [1,1,0,0],
      opChara : [1,1,1],
      opCreation : [1,1],
      opConversion : [1,1],

      // opKariOrder : 0,
      // opKariDuration : 0,
      // opKariLength : 0,
      // opKariRating : [1,0,0],
      // opKariCreation : [1,1],

      navClass : '',
      // allPostDataList: [],
      dispDataList: [],
      favoriteTagList: [],

      errorMessageData : {
        keyword : "",
      }
    };
  },
  computed : {
    btnClass : function() {
      return (val1, val2) => {
        if (val1 == val2) {
          return this.filBtnOn;
        } else {
          return this.filBtnOff;
        }
      }
    },
  },
  methods : {
    test : function () {
      this.$root.log("IN");
      let params = new URLSearchParams();
      params.append('aaa', this.$root.AAA);
      params.append('bbb', this.$root.BBB);
      axios.post(this.$root.SERVER_PATH + 'test', params)
      .then((response) => {
        this.$root.log(response.data);
      });
    },
    pushSearch : function () {
      this.$root.showLoad();

      this.errorMessageData.keyword = "";

      let query = Object.assign({}, this.$route.query);
      let curPage = query["page"];
      let curKeyword = query["keyword"];
      let curFilter = query["fil"];

      //ワードのチェック
      let keyword = this.seaKeyword;
      keyword = this.$root.arrangeStr(keyword, ' ');
     
      if (keyword.length > 50) {
        this.errorMessageData.keyword = "検索ワードの文字数は50文字以下で入力してください";
        this.$root.closePopup();
        return;
      }

      //現在のフィルター設定
      let filStr = String(this.opOrder) + 
      String(this.opDuration) + 
      String(this.opLength) + 
      String(this.opRating[0]) + 
      String(this.opRating[1]) + 
      String(this.opRating[2]) + 
      String(this.opRating[3]) + 
      String(this.opChara[0]) + 
      String(this.opChara[1]) + 
      String(this.opChara[2]) + 
      String(this.opCreation[0]) + 
      String(this.opCreation[1]) +
      String(this.opConversion[0]) + 
      String(this.opConversion[1]);
      
      this.isFilterOpen = false;
      
      //絞り込みが変わっているか
      console.log(curPage);
      console.log(curKeyword + " " + keyword);
      console.log(curFilter + " " + filStr);  
      if (curPage == 1 && curKeyword == keyword && curFilter == filStr) {
        console.log("変更なし");
        window.scrollTo(0,0);
        this.$root.closePopup();
        return;
      } 
 
      this.$root.log("再検索");
      query["keyword"] = keyword;
      query["fil"] = filStr;
      query["page"] = 1;
      this.$router.push({query});
    },
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    settingOpen : function() {
      this.isFilterOpen = true;
    },
    settingClose : function() {
      this.isFilterOpen = false;
    },
    pushReset : function() {
      this.opOrder = 0;
      this.opDuration = 0;
      this.opLength = 0;
      this.opRating = [1,1,0,0];
      this.opChara = [1,1,1];
      this.opCreation = [1,1];
      this.opConversion = [1,1];
    },
    pushTag : function (tag) {
      let query = Object.assign({}, this.$route.query);
      query["keyword"] = tag;
      this.$router.push({query});
    },
    updateSetting : function() {
      let query = Object.assign({}, this.$route.query);
      query["page"] = 1;
      query["fil"] = String(this.opOrder) + 
      String(this.opDuration) + 
      String(this.opLength) + 
      String(this.opRating[0]) + 
      String(this.opRating[1]) + 
      String(this.opRating[2]) + 
      String(this.opRating[3]) + 
      String(this.opChara[0]) + 
      String(this.opChara[1]) + 
      String(this.opChara[2]) + 
      String(this.opCreation[0]) + 
      String(this.opCreation[1]) +
      String(this.opConversion[0]) + 
      String(this.opConversion[1]);
      this.$router.push({query});
      this.navClass = '';
    },
    pushSeOpOrder : function(val) {
      this.opOrder = val;
    },
    pushSeOpDuration : function(val) {
      this.opDuration = val;
    },
    pushSeOpLength : function(val) {
      this.opLength = val;
    },
    getPostList(flg) {
      this.$root.log("新規データ取得");
      let filStr = String(this.opOrder) + 
      String(this.opDuration) + 
      String(this.opLength) + 
      String(this.opRating[0]) + 
      String(this.opRating[1]) + 
      String(this.opRating[2]) + 
      String(this.opRating[3]) + 
      String(this.opChara[0]) + 
      String(this.opChara[1]) + 
      String(this.opChara[2]) + 
      String(this.opCreation[0]) + 
      String(this.opCreation[1]) +
      String(this.opConversion[0]) + 
      String(this.opConversion[1]);

      this.$root.log("PAGE " + this.currentPage);
      let params = new URLSearchParams();
      params.append('keyword', this.seaKeyword);
      params.append('filter', filStr);
      params.append('test', "");
      params.append('page', this.currentPage);
      
      axios.post(this.$root.SERVER_PATH + 'getPostList', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        let resData = response.data;
        let resDataList = resData["postList"];
        this.dispDataList = [];
        for (let i = 0; i < resDataList.length; i++) {
          let rowData = resDataList[i];

          var tagStr = rowData["tags"];
          var tagArray = {};
          if (tagStr != "") {
            tagArray = tagStr.split(',');
          }

          var data = {
            postId : rowData["post_id"],
            userId : rowData["user_id"],
            listType : "listTypeE",
            author : rowData["user_name"],
            series : rowData["series"],
            title : rowData["title"],
            outline : rowData["outline"],
            rating : rowData["rating"],
            chara : rowData["chara"],
            creation : rowData["creation"],
            tags : tagArray,
            length : rowData["length"],
            postDate : new Date(rowData["created_at"])
          };
          this.dispDataList.push(data);
        }

        this.totalCount = resData["count"];

        var page = Math.floor(this.totalCount / 10);
        if (this.totalCount % 10 != 0) {
          page = page + 1;
        }
        this.totalPages = page;
        
        //お気に入り
        let baseInfo = resData["baseInfo"];
        this.favoriteTagList = [];
        if (baseInfo["favorite_tag"] != "") {
          this.favoriteTagList = baseInfo["favorite_tag"].split(',');
        }
        this.$root.log("LEN " + this.favoriteTagList.length);
        if (flg) {
          let query = Object.assign({}, this.$route.query);
          query["page"] = 1;
          this.$router.push({query});      
        }

        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    beforeAction : function() {
      let query = Object.assign({}, this.$route.query);
      let okFlg = true;

      var qFil = query["fil"];
      // let currentFil = query["fil"];
      if (qFil == null || qFil.length > 14) {
        qFil = "00010011111111";
        okFlg = false;
      }
      var qPage = query["page"];
      if (qPage == null || parseInt(qPage) < 1) {
        qPage = 1;
        okFlg = false;
      }
      var qKeyword = query["keyword"];
      if (qKeyword == null) {
        qKeyword = "";
        okFlg = false;
      }

      if (okFlg == false) {
        //この時点でアウトの場合
        query["fil"] = qFil;
        query["page"] = qPage;
        query["keyword"] = qKeyword;
        this.$router.replace({query});       
      } else {
        //フィルターチェック
        let opOrder = parseInt(qFil.substr(0, 1));
        if (opOrder != 0 && opOrder != 1 && opOrder != 2) {
          okFlg = false;
          opOrder = "0";
        }
        let opDuration = parseInt(qFil.substr(1, 1));
        if (opDuration != 0 && opDuration != 1 && opDuration != 2 && opDuration != 3) {
          okFlg = false;
          opDuration = "0";
        }
        let opLength = parseInt(qFil.substr(2, 1));
        if (opLength != 0 && opLength != 1 && opLength != 2 && opLength != 3) {
          okFlg = false;
          opLength = "0";
        }
        let opRating1 = parseInt(qFil.substr(3, 1));
        if (opRating1 != 0 && opRating1 != 1) {
          okFlg = false;
          opRating1 = 1;
        }
        let opRating2 = parseInt(qFil.substr(4, 1));
        if (opRating2 != 0 && opRating2 != 1) {
          okFlg = false;
          opRating2 = 1;
        }
        let opRating3 = parseInt(qFil.substr(5, 1));
        if (opRating3 != 0 && opRating3 != 1) {
          okFlg = false;
          opRating3 = 0;
        }
        let opRating4 = parseInt(qFil.substr(6, 1));
        if (opRating4 != 0 && opRating4 != 1) {
          okFlg = false;
          opRating4 = 0;
        }
        let opChara1 = parseInt(qFil.substr(7, 1));
        if (opChara1 != 0 && opChara1 != 1) {
          okFlg = false;
          opChara1 = 1;
        }
        let opChara2 = parseInt(qFil.substr(8, 1));
        if (opChara2 != 0 && opChara2 != 1) {
          okFlg = false;
          opChara2 = 0;
        }
        let opChara3 = parseInt(qFil.substr(9, 1));
        if (opChara3 != 0 && opChara3 != 1) {
          okFlg = false;
          opChara3 = 0;
        }
        let opCreation1 = parseInt(qFil.substr(10, 1));
        if (opCreation1 != 0 && opCreation1 != 1) {
          okFlg = false;
          opCreation1 = 1;
        }
        let opCreation2 = parseInt(qFil.substr(11, 1));
        if (opCreation2 != 0 && opCreation2 != 1) {
          okFlg = false;
          opCreation2 = 1;
        }
        let opConversion1 = parseInt(qFil.substr(12, 1));
        if (opConversion1 != 0 && opConversion1 != 1) {
          okFlg = false;
          opConversion1 = 1;
        }
        let opConversion2 = parseInt(qFil.substr(13, 1));
        if (opConversion2 != 0 && opConversion2 != 1) {
          okFlg = false;
          opConversion2 = 1;
        }

        if (okFlg == false) {
          //この時点でアウトの場合
          query["fil"] = opOrder + opDuration + opLength + opRating1 + opRating2 + opRating3 + opChara1 + opChara2 + opChara3 + opCreation1 + opCreation2;
          query["page"] = qPage;
          query["keyword"] = qKeyword;
          this.$router.replace({query});
        } else {
          this.opOrder = opOrder
          this.opDuration = opDuration;
          this.opLength = opLength;
          this.opRating = [opRating1, opRating2, opRating3, opRating4];
          this.opChara = [opChara1, opChara2, opChara3];
          this.opCreation = [opCreation1, opCreation2];
          this.opConversion = [opConversion1, opConversion2];

          // this.opKariOrder = this.opOrder;
          // this.opKariDuration = this.opDuration;
          // this.opKariLength = this.opLength;
          // this.opKariRating = this.opRating;
          // this.opKariCreation = this.opCreation;

          this.currentPage = parseInt(qPage);
          this.filStr = "" + opOrder + opDuration + opLength + opRating1 + opRating2 + opRating3 + opRating4 + opChara1 + opChara2 + opChara3 + opCreation1 + opCreation2 + opConversion1 + opConversion2;
          this.seaKeyword = qKeyword;
          this.getPostList();
        }
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
      vm.$root.setCurrentPage("SEARCH");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();    
      vm.beforeAction();
      vm.$root.setCurrentPage("SEARCH");
    });
  },
  watch : {
    '$route'(to) {
      console.log(to);
      if (to["name"] == "Search") {
        // let toQuery = to.query;
        // let fromQuery = from.query;

        // var tFil = toQuery["fil"];
        // if (tFil == null) {
        //   tFil = "00010011";
        // }
        // var tPage = toQuery["page"];
        // if (tPage == null) {
        //   tPage = 1;
        // }
        // var tKeyword = toQuery["keyword"];
        // if (tKeyword == null) {
        //   tKeyword = "";
        // }

        // var fFil = fromQuery["fil"];
        // if (fFil == null) {
        //   fFil = "00010011";
        // }
        // var fPage = fromQuery["page"];
        // if (fPage == null) {
