<script>
import { getAuth, setPersistence, TwitterAuthProvider, signInWithPopup, browserLocalPersistence } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'Setting',
  components: {
  },
  data() {
    return {
      setUserName : "",
      setProfile : "",
      setLinkList : [["", ""]],
      setMuteUserList : {},
      setFavoriteTag : "",
      setMuteTag : "",
      setIconFileName : "",
      setIconImg : "",
      setIconFile : null,
      setTwitterCode : "",
      setMailAddress : "",
      setShowTwitter : false,
      setDispR18 : false,
      setDispR18g : false,
      setInfo : false,
      chMailAddress : "",
      authMailAddress : "",
      authPassword : "",
      authNumbers : "",

      currentPassword : "",
      newPassword1 : "",
      newPassword2 : "",

      isChangeIcon : false,
      liftIndex : -1,

      curLinkList : [],

      errorMessageData : {
        icon : "",
        userName : "",
        profile : "",
        link : "",
        favorite : "",
        mute : "",

        mailAddress : "",
        password : "",
        numbers : "",
        chMailAddress: "",
        currentPassword : "",
        newPassword1 : "",
        newPassword2 : "",

        resChMail : "",
        resCertificateTwitter : "",
        resAuthMail : "",
        resChPassword : "",
      },

      popupKbn : "",
      popupParams : {
        title : "",
        body : "",
        tweet : "",
        muteUserList : [],
      },
    };
  },
  computed : {
    setMypageUrl : function() {
      return this.$root.BASE_URL + "userpage/" + this.$root.userData.userId;
    },
    theme_switch : function() {
        if (this.$root.themeIndex == 0) {
          return "theme_switch_1";
        } else if (this.$root.themeIndex == 1) {
          return "theme_switch_2";
        } else if (this.$root.themeIndex == 2) {
          return "theme_switch_3";
        } else if (this.$root.themeIndex == 3) {
          return "theme_switch_4";
        }
        return "theme_switch_1";
    }
  },
  methods : {
    shareUrl : function () {
      var url = "https://twitter.com/intent/tweet?" + 
        "hashtags=" + encodeURIComponent('yumedrop,夢小説,テスト') + 
        "&text=" + encodeURIComponent("ユメドロップ自己紹介ページだよ") + 
        "&url=" + encodeURIComponent(this.$root.BASE_URL + "userpage/" + this.$root.userData.userId) + 
        "&via=" + encodeURIComponent("iphonetaro_live");
        window.open(url);
    },
    pushAddLink : function() {
      this.setLinkList.push(["", ""]);
    },
    pushCertificateMail : function () {
      this.$root.showLoad();

      this.errorMessageData.mailAddress = "";
      this.errorMessageData.password = "";
      this.errorMessageData.resAuthMail = "";

      //トリム
      this.authMailAddress = this.authMailAddress.trim();
      this.authPassword = this.authPassword.trim();
      
      //入力チェック
      if (this.authMailAddress == "") {
        this.errorMessageData.mailAddress = "ID（メールアドレス）の文字数は1文字以上入力してください";
      } else if (this.authMailAddress.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.mailAddress = "ID（メールアドレス）の文字数は200文字以下で入力してください";
      } else {
        let val = this.$root.checkAddress(this.authMailAddress);
        if (val == 1) {
          this.errorMessageData.mailAddress = "ID（メールアドレス）に誤りがあります。もう一度入力してください";
        } else if (val == 2) {
          this.errorMessageData.mailAddress = "該当のメールアドレスのドメインでは登録できません。別のメールアドレスでご登録ください";
        }
      }
      
      if (this.authPassword.length < 8) {
        this.errorMessageData.password = "パスワードの文字数は8文字以上入力してください";
      } else if (this.authPassword.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.password = "パスワードの文字数は32文字以下で入力してください";
      } else if (!this.$root.checkPassword(this.authPassword)) {
        console.log(this.authPassword);
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.errorMessageData.mailAddress != "" || this.errorMessageData.password != "") {
        this.$root.closePopup();
        return;
      }
      
      //仮登録処理
      let params = new URLSearchParams();
      params.append('mailAddress', this.authMailAddress);
      params.append('password', this.authPassword);
      params.append('kbn', "ADD");
      //axios.get('/sanctum/csrf-cookie').then(response => {
      axios.post(this.$root.SERVER_PATH + 'reqAuthMail', params)
      .then((response) => {
        let resData = response.data;

        if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
          //仮登録エラー（本登録重複）
          this.errorMessageData.resAuthMail = "このメールアドレスは既に使用されています";
          this.$root.closePopup();
          return;
        }

        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        //ポップアップを出す
        this.popupKbn = "CERTIFICATION";
        this.popupParams = {
          title : "認証用メール発行",
          body : "メールアカウント登録の確認メールを送信しました。受信したメールに記載されているURLより、手続きを完了してください。",
        }
        this.$root.log("http://localhost:8001/registrationConfirmation/" + resData["temp_id"]);

        this.$root.closePopup();
        this.$refs.popup.showModal();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    pushChangeMail : function () {
      this.$root.showLoad();
      
      this.errorMessageData.chMailAddress = "";
      this.errorMessageData.resChMail = "";
      //トリム
      this.chMailAddress = this.chMailAddress.trim();

      if (this.chMailAddress == "") {
        this.errorMessageData.chMailAddress = "ID（メールアドレス）の文字数は1文字以上入力してください";
      } else if (this.chMailAddress.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.chMailAddress = "ID（メールアドレス）の文字数は200文字以下で入力してください";
      } else {
        let val = this.$root.checkAddress(this.chMailAddress);
        if (val == 1) {
          this.errorMessageData.chMailAddress = "ID（メールアドレス）に誤りがあります。もう一度入力してください";
        } else if (val == 2) {
          this.errorMessageData.chMailAddress = "該当のメールアドレスのドメインでは登録できません。別のメールアドレスでご登録ください";
        }
      }

      if (this.errorMessageData.chMailAddress != "") {
        this.$root.closePopup();
        return;
      }

      //仮登録処理
      let params = new URLSearchParams();
      params.append('mailAddress', this.chMailAddress);
      params.append('kbn', "CHANGE");
