    pushChangePassword : function () {
      this.$root.showLoad();

      this.errorMessageData.currentPassword = "";
      this.errorMessageData.newPassword1 = "";
      this.errorMessageData.newPassword2 = "";
      this.errorMessageData.resChPassword = "";
      //トリム
      this.currentPassword = this.currentPassword.trim();
      this.newPassword1 = this.newPassword1.trim();
      this.newPassword2 = this.newPassword2.trim();
      
      //入力チェック
      if (this.currentPassword.length < 8) {
        this.errorMessageData.currentPassword = "現在のパスワードの文字数は8文字以上入力してください";
      } else if (this.currentPassword.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.currentPassword = "パスワードの文字数は32文字以下で入力してください";
      } else if (!this.$root.checkPassword(this.currentPassword)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.newPassword1.length < 8) {
        this.errorMessageData.newPassword1 = "新しいパスワードの文字数は8文字以上入力してください";
      } else if (this.newPassword1.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.newPassword1 = "パスワードの文字数は32文字以下で入力してください";
      } else if (!this.$root.checkPassword(this.newPassword1)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.newPassword2.length < 8) {
        this.errorMessageData.newPassword2 = "新しいパスワード（確認）の文字数は8文字以上入力してください";
      } else if (this.newPassword2.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.newPassword2 = "パスワードの文字数は32文字以下で入力してください";
      } else if (!this.$root.checkPassword(this.newPassword2)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.errorMessageData.currentPassword != "" || this.errorMessageData.newPassword1 != "" || this.errorMessageData.newPassword2 != "") {
        this.$root.closePopup();
        return;
      }
      if (this.newPassword1 != this.newPassword2) {
        this.errorMessageData.newPassword1 = "正しい新しいパスワード（確認）を入力してください";
        this.$root.closePopup();
        return;
      }
      if (this.currentPassword == this.newPassword1) {
        this.errorMessageData.newPassword1 = "現在のパスワードと異なる値を入力してください";
        this.$root.closePopup();
        return;
      }

      //変更処理
      let params = new URLSearchParams();
      params.append('currentPassword', this.currentPassword);
      params.append('newPassword', this.newPassword1);
      //axios.get('/sanctum/csrf-cookie').then(response => {
      axios.post(this.$root.SERVER_PATH + 'updateAuthPassword', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_MISMATCH) {
          //パスワード不一致
          this.errorMessageData.resChPassword = "現在のパスワードが正しくありません";
          this.$root.closePopup();
          return;
        }

        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        this.popupKbn = "CERTIFICATION";
        this.popupParams = {
          title : "変更完了",
          body : "パスワードを変更しました",
        }

        this.$root.closePopup();
        this.$refs.popup.showModal();

        this.currentPassword = "";
        this.newPassword1 = "";
        this.newPassword2 = "";
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
