    onFileChange(e) {
      this.$root.showLoad();

      this.errorMessageData.icon = "";
      const files = e.target.files || e.dataTransfer.files;
      if (files.length == 0) {
        this.$root.closePopup();
        return;
      }
      let size = files[0].size;
      let type = files[0].type;

      //チェック
      if (size > 1024000) {
        //サイズオーバー
        this.errorMessageData.icon = "アイコンのサイズは1MB以下にしてください";
        this.$root.closePopup();
        return;
      }
      if (type != "image/jpeg" && type != "image/png") {
        //形式エラー
        this.errorMessageData.icon = "アイコンの形式はpngまたはjpegにしてください";
        this.$root.closePopup();
        return;
      }
      this.$root.log(size + " " + type);

      this.createImage(files[0]);
      this.setIconFile = files[0];
      this.setIconFileName = files[0].name;
      this.isChangeIcon = true;

      this.$root.closePopup();
    },
    createImage(file) {
      const reader = new FileReader();
      reader.onload = e => {
        this.setIconImg = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    remove() {
      this.uploadedImage = false;
    },
