    pushUpdateFavorite : function () {
      this.$root.showLoad();

      this.errorMessageData.favorite = "";

      //トリム
      this.setFavoriteTag = this.setFavoriteTag.trim();
      //#がついていれば除去
      this.setFavoriteTag = this.$root.arrangeStr(this.setFavoriteTag, ',');
      
      let tagArray = this.setFavoriteTag.split(',');
      if (tagArray.length > 8) {
        this.errorMessageData.favorite = "お気に入りタグは8個まで登録できます";
      } else {
        for (let i = 0; i < tagArray.length; i++) {
          const element = tagArray[i];
          if (element.length > 20) {
            this.errorMessageData.favorite = "お気に入りタグは1つあたり20文字以下で入力してください";
          } else {
            let regStr = this.$root.checkRegulation(element)  
            if (regStr != "") {
              this.errorMessageData.favorite = this.$root.STR_ERROR_REGULATION + regStr;
            }
          }
        }
      }

      if (this.errorMessageData.favorite != "") {
        this.$root.closePopup();
        return;
      }
      
      //成型
      let favArray = this.setFavoriteTag.split(",");
      let favStr = "";
      for (let i = 0; i < favArray.length; i++) {
        //空文字を除去
        if (favArray[i] != "") {
          favStr = favStr + favArray[i];

          if (i != favArray.length - 1) {
            favStr = favStr + ",";
          }
        }        
      }
      this.setFavoriteTag = favStr;

      //前回と同じか
      if (this.setFavoriteTag == this.$root.userData.favorite) {
        this.$root.closePopup();
        return;
      }

      let params = new URLSearchParams();
      params.append('favorite', this.setFavoriteTag);

      axios.post(this.$root.SERVER_PATH + 'updateSettingFavorite', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        this.$root.userData.favorite = this.setFavoriteTag;

        this.popupKbn = "UPDATE";
          this.popupParams = {
            title : "お気に入りタグ",
            body : "変更しました",
          }

          this.$root.closePopup();
          this.$refs.popup.showModal();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
      // this.errorMessageData.favorite = "お気に入りがだめー";
    },
